import React from "react";
import {Card,CardContent,Divider,Grid,TextField, Button, Autocomplete, Select } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import SaveOutlinedIcon from '@material-ui/icons/SaveOutlined';
import brands from "../../../../assets/dropdown-data/brand";
import { FormControl } from "@material-ui/core";
import { InputLabel } from "@material-ui/core";
import { MenuItem } from "@material-ui/core";
import { Checkbox } from "@material-ui/core";
import { useState } from "react";
import { FormControlLabel } from "@material-ui/core";
const AddDiscountModal = ({closeModal}) => {
    const [isActive,setIsActive] = useState(false);
    const handleIsActive = ()=>{
        setIsActive(!isActive);
    }
  return (
    <>
      <Grid container>
        <Grid item lg={3} md={3}/>
        <Grid item lg={6} md={6}>
        <Card>
            <CardContent>
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                  <Grid item lg={6} md={6}>
                    <h1>Add Discount</h1>
                  </Grid>
                  <Grid item lg={3} md={3}/>
                  <Grid item lg={2} md={2}/>
                  <Grid item lg={1} md={1}>
                    <h5><CloseIcon color="error" onClick={closeModal} style={{cursor:"pointer"}}/></h5>
                  </Grid>
                </Grid>
            </CardContent>
            <Divider/>
            <CardContent>
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={12} md={12}>
                        <TextField
                            fullWidth
                            variant="outlined"
                            label="Name"
                        />
                    </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                      <Grid item lg={12} md={12}>
                        <TextField
                                fullWidth
                                variant="outlined"
                                label="Product"
                            />
                     </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                      <Grid 
                      container
                      direction="row"
                      spacing="2">
                          <Grid item lg={6} md={6}>
                            <Autocomplete
                                fullWidth
                                id="combo-box-demo"
                                options={brands}
                                getOptionLabel={(option) => option.name}
                                renderInput={(params) => <TextField  {...params} label="Brands" variant="outlined" />}
                                />
                          </Grid>
                          <Grid item lg={6} md={6}>
                            <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Category</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Category"
                                    >
                                    <MenuItem value="">Please Select</MenuItem>
                                    <MenuItem value="Men's">Men's</MenuItem>
                                    <MenuItem value="Women's">Women's</MenuItem>
                                    <MenuItem value="Accesories">Accesories</MenuItem>
                                    <MenuItem value="Electronics">Electronics</MenuItem>
                                    </Select>
                                </FormControl>
                          </Grid>
                      </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                      <Grid 
                      container
                      direction="row"
                      spacing="2">
                          <Grid item lg={6} md={6}>
                          <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Location</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Location"
                                    >
                                    <MenuItem value="">Please Select</MenuItem>
                                    <MenuItem value="Shop Banani">Shop Banani</MenuItem>
                                    <MenuItem value="Awesome Shop">Awesome Shop</MenuItem>
                                    </Select>
                                </FormControl>
                          </Grid>
                          <Grid item lg={6} md={6}>
                                <TextField
                                fullWidth
                                type="number"
                                label="Priority"
                                variant="outlined"/>
                          </Grid>
                      </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                      <Grid 
                      container
                      direction="row"
                      spacing="2">
                          <Grid item lg={6} md={6}>
                          <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Discount Type</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Discount Type"
                                    >
                                    <MenuItem value="">Please Select</MenuItem>
                                    <MenuItem value="Fixed">Fixed</MenuItem>
                                    <MenuItem value="Percentage">Percentage</MenuItem>
                                    </Select>
                                </FormControl>
                          </Grid>
                          <Grid item lg={6} md={6}>
                                <TextField
                                fullWidth
                                type="number"
                                label="Discount Amount"
                                variant="outlined"/>
                          </Grid>
                      </Grid>
                    </Grid>
                    <br />
                    <Grid 
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center" 
                    spacing="1">
                        <Grid 
                        container
                        direction="row"
                        spacing="2">
                            <Grid item lg={6} md={6}>
                                <TextField
                                        fullWidth
                                        type="date"
                                        label="Start Date"
                                        variant="outlined"/>
                            </Grid>
                            <Grid item lg={6} md={6}>
                                    <TextField
                                    fullWidth
                                    type="date"
                                    label="End Date"
                                    variant="outlined"/>
                            </Grid>
                        </Grid>
                        </Grid>
                        <br />
                        <Grid 
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center" 
                    spacing="1">
                        <Grid 
                        container
                        direction="row"
                        spacing="2">
                            <Grid item lg={6} md={6}>
                                <FormControlLabel
                                    control={<Checkbox checked={isActive} onChange={handleIsActive} name="checkedA" />}
                                    label="Is Active?"
                                    />
                            </Grid>
                            <Grid item lg={6} md={6}/>
                        </Grid>
                        </Grid>
            </CardContent>
            <Divider/>
            <CardContent>
                <Grid 
                container
                direction="row"
                justifyContent="center"
                alignItems="center" 
                spacing="1">
                  <Grid item lg={12} md={12}>
                      <Button
                        fullWidth
                        variant="contained"
                        color="success"
                        endIcon={<SaveOutlinedIcon/>}
                      >
                        Save
                      </Button>
                  </Grid>
                </Grid>
            </CardContent>
        </Card>
        </Grid>
        <Grid item lg={3} md={3}/>
      </Grid>
  </>
  );
};

export default AddDiscountModal;
