import React from "react";
import { Grid } from "@material-ui/core";
import RegisterFilter from "../Components/Register/RegisterReportFilter";
import RegisterReportTable from "../Components/Register/RegisterTable";

const RegisterReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Register Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <RegisterFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <RegisterReportTable/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default RegisterReport;
