import React from "react";
import { Grid, Card, Autocomplete } from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import InfoIcon from '@material-ui/icons/Info';
import EventIcon from '@material-ui/icons/Event';
import { CardContent } from "@material-ui/core";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { supplierList } from "./Suppliers";
import { TextField } from "@material-ui/core";
const ViewSupplier = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
          <Grid container>
            <Grid item lg={6} md={6}>
              <h2>Supplier Details</h2>
            </Grid>
            <Grid item lg={6} md={6}>
              <Autocomplete
                      fullWidth
                      id="combo-box-demo"
                      options={supplierList}
                      getOptionLabel={(option) => option.name}
                      renderInput={(params) => <TextField  {...params} label="Search Supplier" variant="outlined" />}
                      />
            </Grid>
          </Grid>
          <Grid container>
            <Grid 
            style={{ marginBottom: "15px" }}
            item 
            lg={12} md={12}>
                <Card>
                  <CardContent>
                    <Grid container>
                      <Grid item lg={12} md={12}>
                        <h2><AccountCircleIcon/> Philip</h2>
                      </Grid>
                    </Grid>
                    <Grid container>
                      <Grid item lg={6} md={6}>
                        <Grid container>
                          <Grid item lg={3} md={3}>
                            <h4><LocationOnIcon/>Address</h4>
                            <p>Manhattan Clothing Ltd.
                              , Philip,
                              Linking Street,
                              Phoenix, Arizona, USA </p>
                          </Grid>
                        </Grid>
                        <Grid container>
                          <Grid item lg={3} md={3}>
                            <h4><BusinessCenterIcon/>Business Name</h4>
                            <p>Manhattan Clothing Ltd. </p>
                          </Grid>
                        </Grid>
                        <Grid container>
                          <Grid item lg={3} md={3}>
                            <h4><PhoneIphoneIcon/>Mobile</h4>
                            <p>(378) 400-1234 </p>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item lg={6} md={6}>
                        <Grid container>
                          <Grid item lg={3} md={3}>
                            <h4><InfoIcon/>Tax Number</h4>
                            <p>54869310093 </p>
                          </Grid>
                        </Grid>
                        <Grid container>
                          <Grid item lg={3} md={3}>
                            <h4><EventIcon/>Pay Term Period</h4>
                            <p>Days</p>
                          </Grid>
                        </Grid>
                        <Grid container>
                          <Grid item lg={3} md={3}>
                            <h4><InfoIcon/>Pay Term</h4>
                            <p>15</p>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  </CardContent>
                </Card>
            </Grid>
          </Grid>
      </Grid>
  );
};

export default ViewSupplier;
