import React,{useRef} from "react";
import {Card,CardContent,Divider,Grid, Button,Avatar, FormControl, RadioGroup, FormControlLabel, Radio } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import SaveOutlinedIcon from '@material-ui/icons/SaveOutlined';
import ReactToPrint from "react-to-print";
import PosContext from "../PosContext";
const InvoiceModal = ({closeModal}) => {
    const componentRef = useRef();
    const date = new Date();
    const months = ["January","February","March","April","May","June","July","August","September","October","November","December"];
    const getTime = ()=> {
        var hours = date.getHours();
        var minutes = date.getMinutes();
        var ampm = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12;
        hours = hours ? hours : 12; // the hour '0' should be '12'
        minutes = minutes < 10 ? '0'+minutes : minutes;
        var strTime = hours + ':' + minutes + ' ' + ampm;
        return strTime;
      }
    const getFullDate = ()=>{
        const month = months[date.getMonth()];
        const dateToString = date.getDate();
        const year = date.getFullYear();
        return `${month} ${dateToString}, ${year}`;
    }
  return (
    <PosContext.Consumer>
        {context=>(
            <>
            <Grid container style={{ height: "100vh",overflowY:"scroll" }}>
              <Grid item lg={3} md={3}/>
              <Grid item lg={6} md={6}>
              <Card>
                  <CardContent>
                      <Grid 
                        container
                        direction="row"
                        justifyContent="center"
                        alignItems="center" 
                        spacing="1">
                        <Grid item lg={6} md={6}>
                          <h1>Invoice</h1>
                        </Grid>
                        <Grid item lg={3} md={3}/>
                        <Grid item lg={2} md={2}/>
                        <Grid item lg={1} md={1}>
                          <h5><CloseIcon color="error" onClick={closeModal} style={{cursor:"pointer"}}/></h5>
                        </Grid>
                      </Grid>
                      <Grid
                      container
                      direction="row"
                      justifyContent="center"
                      alignItems="center" 
                      spacing="1">
                      <Grid 
                        style={{ marginBottom:"15px" }}
                        item lg={6} md={6}>
                            <FormControl component="fieldset">
                                <RadioGroup aria-label="gender" value={context.printType} onChange={(event)=>context.setPrintType(event.target.value)} name="gender1">
                                    <Grid container
                                        direction="row"
                                        spacing="2">
                                        <Grid item lg={6} md={6}>
                                            <FormControlLabel value="Customer" control={<Radio />} label="Customer" />
                                        </Grid>
                                        <Grid item lg={6} md={6}>
                                            <FormControlLabel value="Kitchen" control={<Radio />} label="Kitchen" />
                                        </Grid>
                                    </Grid>
                                </RadioGroup>
                            </FormControl>
                        </Grid>
                      </Grid>
                  </CardContent>
                  <Divider/>
                  <CardContent 
                  ref={componentRef} 
                  style={{ fontSize:"10px",margin:"0 auto",width:"40mm" }}>
                      <div>
                          <Avatar 
                          style={{ margin:"0 auto",width:"20mm",height:"10mm",
                          backgroundPosition:"center",backgroundRepeat:"no-repeat",
                          backgroundSize:"cover" }} 
                          src="https://res.cloudinary.com/chiranswe/image/upload/v1633716259/desha_logo-removebg-preview_xf8sec.png"/>
                      </div>
                      <div>
                          <div style={{ textAlign:"center" }}>
                              <span>DESHA Tower, Upazila Moor</span><br />
                              <span>Kushtia-Jhenaidah Highway, Kushtia</span><br />
                              <span>Mobile No: 01720510368,01720510311</span><br />
                              <span>Mushak: 2.3</span><br />
                              <span>Bin :002535974-0903</span>
                          </div>
                          <div style={{ display:"flex" }}>
                              <span style={{ width:"30%",height:"2mm",alignItem:"center",background:"rgba(0,0,0,0.1)" }}></span>
                              <span style={{ width:"40%",textAlign:"center"}}>Guest Bill</span>
                              <span style={{ width:"30%",height:"2mm",alignItem:"center",background:"rgba(0,0,0,0.1)" }}></span>
                          </div>
                          <div>
                              <span>Table: {context.customerTableNo}</span><br />
                              <span>Waiter: {context.waiter}</span><br />
                              <span>Customer: {context.customerName}</span><br />
                              <span>Phone: {context.customerPhone}</span>
                          </div>
                          <div style={{ display:"flex",marginTop:"0" }}>
                              <div style={{ width:"70%"}}>
                                <span>Date: {getFullDate()}</span><br />
                                <span>Number Of Guests: {context.guestNumber}</span><br />
                                <span>Chalan No: 1776</span><br />
                                <span>NRB Invoice No: 002121XMYVETY086</span><br />
                                <span style={{ fontSize:"13px" }}><strong>{context.paymentStatus}</strong></span>
                              </div>
                              <div style={{ width:"30%",float:"right"}}>
                                  <span>Time: {getTime()}</span>
                              </div>
                          </div>
                      </div>
                      <div style={{ display: "flex",
                      borderTop:"2px dotted rgba(0,0,0,0.3)", borderBottom:"2px dotted rgba(0,0,0,0.3)" }}>
                              <span style={{ width:"20%" }}>-Qty</span><br />
                              <span style={{ width:"40%" }}>Item Name</span><br />
                              <span style={{ width:"20%" }}>Price</span><br />
                              <span style={{ width:"20%" }}>T.Price</span>
                      </div>
                      <div style={{borderBottom:"2px dotted rgba(0,0,0,0.3)" }}>
                          {context.invoiceItemList.map((item)=>{
                              return (
                                  <div style={{ display:"flex" }}>
                                    <span style={{ width:"20%" }}>-{item.quantity}</span><br />
                                    <span style={{ width:"40%" }}>{item.name}</span><br />
                                    <span style={{ width:"20%" }}>{item.price}</span><br />
                                    <span style={{ width:"20%" }}>{item.subtotal}</span>
                                  </div>
                              )
                          })}
                      </div>
                      {context.printType === "Kitchen" ? (<div></div>):(
                        <>
                          <div style={{ display: "flex",margin:"1px 0",
                          borderBottom:"2px dotted rgba(0,0,0,0.3)",borderTop:"2px dotted rgba(0,0,0,0.3)" }}>
                                <span style={{ width:"80%" }}><strong>Gross Total</strong></span>
                                <span style={{ width:"20%" }}>{context.grossTotal}</span>
                          </div>
                          <div style={{margin:"1px 0",
                          borderBottom:"2px dotted rgba(0,0,0,0.3)",borderTop:"2px dotted rgba(0,0,0,0.3)" }}>
                                <div style={{ display:"flex" }}>
                                    <span style={{ width:"80%" }}><strong>-{context.vat}%</strong></span>
                                    <span style={{ width:"20%" }}>{context.vatAmount}</span>
                                </div>
                          </div>
                          <div style={{margin:"1px 0",
                          borderBottom:"2px dotted rgba(0,0,0,0.3)",borderTop:"2px dotted rgba(0,0,0,0.3)" }}>
                                <div style={{ display:"flex" }}>
                                    <span style={{ width:"80%" }}><strong>NET TOTAL</strong></span>
                                    <span style={{ width:"20%" }}>{context.grandTotal}</span>
                                </div>
                                <div style={{ display:"flex" }}>
                                    <span style={{ width:"80%" }}><strong>TOTAL PAID</strong></span>
                                    <span style={{ width:"20%" }}>{context.paidAmount}</span>
                                </div>
                                <div style={{ display:"flex" }}>
                                    <span style={{ width:"80%" }}><strong>REMAINING AMOUNT</strong></span>
                                    <span style={{ width:"20%" }}>{Number(context.grandTotal-context.paidAmount)}</span>
                                </div>
                          </div>
                        </>
                      )}
                      <div style={{ margin:"1px 0",borderTop:"2px dotted rgba(0,0,0,0.3)" }}>
                          <p>THANK YOU, COME AGAIN</p>
                          <p>Powered by: somikoron it services, www.somikoronits.com</p>
                      </div>
                  </CardContent>
                  <Divider/>
                  <CardContent>
                      <Grid 
                      container
                      direction="row"
                      justifyContent="center"
                      alignItems="center" 
                      spacing="1">
                        <Grid item lg={12} md={12}>
                          <ReactToPrint
                              trigger={() => <Button
                                  fullWidth
                                  variant="contained"
                                  color="success"
                                  endIcon={<SaveOutlinedIcon/>}
                                >
                                  Print
                                </Button>}
                              content={() => componentRef.current}
                          />
                            
                        </Grid>
                      </Grid>
                  </CardContent>
              </Card>
              </Grid>
              <Grid item lg={3} md={3}/>
            </Grid>
          </>
        )}
    </PosContext.Consumer>
  );
};

export default InvoiceModal;
