import React from "react";
import { Grid } from "@material-ui/core";
import ProductSellReportFilter from "../Components/Product Sell/ProductSellFilter";
import ProductSellReportTabs from "../Components/Product Sell/ProductSellTabs";

const ProductSellReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Product Sell Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <ProductSellReportFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <ProductSellReportTabs/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default ProductSellReport;
