import React,{useState} from "react";
import { Grid,Button,MenuItem,Menu, Card } from "@material-ui/core";
import { DataGrid } from '@material-ui/data-grid';
import EditIcon from '@material-ui/icons/Edit';
import { Autocomplete,TextField } from "@material-ui/core";
import AddBoxIcon from '@material-ui/icons/AddBox';
import { Modal } from "@material-ui/core";
import PurchaseFilter from "../Components/PurchaseFilter";
import { Divider } from "@material-ui/core";
import { useNavigate } from "react-router-dom";
export const productPurchaseList = [
  {id: 1,date:"08/31/2021 06:00",referenceno: "PO2018/002",location: "Awesome Shop",supplier:"Univer Suppliers,Jackson Hill"
  ,purchasestatus:"Recieved",paymentstatus:"Due",grandtotal:"235,656",paymentdue:"235,656",addedby:"My Admin"},
]
const PurchaseList = () => {
  const navigate = useNavigate();
    const columns = [
      { field: 'id', headerName: 'ID', width: 120 },
      {
        field: 'date',
        headerName: 'Date',
        width: 180,
        editable: true,
      },
      {
        field: 'referenceno',
        headerName: 'Reference No',
        width: 220,
        editable: true,
      },
      {
        field: 'location',
        headerName: 'Business Location',
        width: 220,
        editable: true,
      },
      {
        field: 'supllier',
        headerName: 'Supllier',
        width: 190,
        editable: true,
      },
      {
        field: 'purchasestatus',
        headerName: 'Purchase Status',
        width: 220,
        editable: true,
      },
      {
        field: 'paymentstatus',
        headerName: 'Payment Status',
        width: 220,
        editable: true,
      },
      {
        field: 'grandtotal',
        headerName: 'Grand Total',
        width: 220,
        editable: true,
      },
      {
        field: 'paymentdue',
        headerName: 'Payment Due',
        width: 220,
        editable: true,
      },
      {
        field: 'addedby',
        headerName: 'Added By',
        width: 190,
        editable: true,
      },
    ];
  const onSelect = (event)=>{
    const {data} = event;
    const {id} = data;
    pushIdToSelectedList(id);
  }
  const [selectedIdList,setSelectedId] = useState([]);
  const pushIdToSelectedList = (id)=>{
      const includes = selectedIdList.includes(id);
      if(includes) {
        const updatedList = selectedIdList.filter((indexId)=> {return id!==indexId});
        setSelectedId(updatedList);
      }else{
          setSelectedId(prevIdList=>[...prevIdList,id]);
      }
  }
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const [addUnitOpenModal,setAddUnitOpenModal] = useState(false);
  const openAddUnitModal = ()=>{
    setAddUnitOpenModal(true);
  }
  const closeAddUnitModal = ()=>{
    setAddUnitOpenModal(false);
  }
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Purchases</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <PurchaseFilter/>
                </Grid>
            </Grid>
            <Card>
            <Grid container >
                <Grid  item lg={3} md={3}>
                    <Button
                      aria-controls="simple-menu" 
                      aria-haspopup="true"
                      onClick={handleMenu}
                      color="success" 
                      variant="contained"
                      startIcon={<EditIcon/>}
                      >
                          Actions
                    </Button>
                        <Menu
                            // style={{ width: "550px" }}
                            id="dd-menu"
                            anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                            anchorOrigin={{ horizontal: "left", vertical: "bottom" }}
                            transformOrigin={{ horizontal: "left", vertical: "top" }}
                            >
                            <MenuItem onClick={openAddUnitModal}>View</MenuItem>
                            <MenuItem onClick={openAddUnitModal}>Print</MenuItem>
                            <MenuItem onClick={openAddUnitModal}>Edit</MenuItem>
                            <MenuItem onClick={openAddUnitModal}>Delete</MenuItem>
                            <MenuItem onClick={handleClose}>Labels</MenuItem>
                            <Divider/>
                            <MenuItem onClick={handleClose}>Add Payment</MenuItem>
                            <MenuItem onClick={handleClose}>View Payments</MenuItem>
                            <MenuItem onClick={handleClose}>Purchase Return</MenuItem>
                            <MenuItem onClick={handleClose}>Update Status</MenuItem>
                            <MenuItem onClick={handleClose}>Items Recieved Notification</MenuItem>
                        </Menu>

                </Grid>
                <Grid item lg={3} md={3}>
                  <Button
                      onClick={()=>{return navigate('purchase/create')}}
                      aria-controls="simple-menu" 
                      aria-haspopup="true"
                      color="success" 
                      variant="contained"
                      startIcon={<AddBoxIcon/>}
                      >
                          Add
                      </Button>
                  
                  {/* add product variation modal */}
                  <Modal
                        open={addUnitOpenModal}
                        onClose={closeAddUnitModal}
                        aria-labelledby="simple-modal-title"
                        aria-describedby="simple-modal-description"
                        >
                        {/* <AddUnitModal 
                          closeModal={closeAddUnitModal}/> */}
                    </Modal>
                </Grid>
                <Grid item lg={6} md={6}>
                  <Autocomplete
                        fullWidth
                        id="combo-box-demo"
                        options={productPurchaseList}
                        getOptionLabel={(option) => option.referenceno}
                        renderInput={(params) => <TextField  {...params} label="Search Purchase" variant="outlined" />}
                        />
                </Grid>
            </Grid>
            <br />
            <Grid container>
                    <Grid item lg={12} md={12}>
                      <div style={{ height: 400, width: '100%' }}>
                          <DataGrid
                              rows={productPurchaseList}
                              columns={columns}
                              pageSize={5}
                              checkboxSelection
                              onRowSelected={onSelect}
                          />
                      </div>
                  </Grid>
            </Grid>
            </Card>
        </Grid>
      </Grid>
  );
};

export default PurchaseList;
