import React from "react";
import { Grid } from "@material-ui/core";
import SellPaymentFilter from "../Components/Sell Payment/SellPaymentFilter";
import SellPaymentTable from "../Components/Sell Payment/SellPaymentTable";

const SellPaymentReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Sell Payment Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SellPaymentFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SellPaymentTable/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default SellPaymentReport;
