import { Autocomplete, Card, InputLabel, Select, Typography } from '@material-ui/core';
import { Divider } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { TextField } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
import { supplierList } from '../../Contacts/Suppliers';
const PurchaseFilter = ()=>{
    return (
        <>
            <Card>
                <CardContent>
                    <Typography variant="h4">Filters</Typography>
                    <br />
                    <Divider/>
                    <br />
                    <Grid container>
                        <Grid item lg={2} md={2}>
                                <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Business Location</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Business Location"
                                    >
                                    <MenuItem value="All">All</MenuItem>
                                    <MenuItem value="Shop1">Shop 1</MenuItem>
                                    </Select>
                                </FormControl>
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={2} md={2}>
                            <Autocomplete
                                fullWidth
                                id="combo-box-demo"
                                options={supplierList}
                                getOptionLabel={(option) => option.name}
                                renderInput={(params) => <TextField  {...params} label="Suplliers" variant="outlined" />}
                                />
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={2} md={2}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Purchase Status</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Purchase Status"
                                >
                                <MenuItem value="All">All</MenuItem>
                                <MenuItem value="Recieved">Recieved</MenuItem>
                                <MenuItem value="Pending">Pending</MenuItem>
                                <MenuItem value="Ordered">Ordered</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={2} md={2}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Payment Status</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Payment Status"
                                >
                                <MenuItem value="All">All</MenuItem>
                                <MenuItem value="Paid">Paid</MenuItem>
                                <MenuItem value="Due">Due</MenuItem>
                                <MenuItem value="Partial">Partial</MenuItem>
                                <MenuItem value="Overdue">Overdue</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>
                    <br />
                    <Grid container>
                        <Grid item lg={2} md={2}>
                            <TextField 
                                type="date"
                                id="outlined-basic" 
                                label="Start Date" 
                                variant="filled" />
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={2} md={2}>
                            <TextField 
                                type="date"
                                id="outlined-basic" 
                                label="End Date" 
                                variant="filled" />
                        </Grid>
                        <Grid item lg={5} md={5}/>
                    </Grid>
                </CardContent>
            </Card>
        </>
    )
}

export default PurchaseFilter;