import React from "react";
import { Grid } from "@material-ui/core";
import SalesRepresentativeFilter from "../Components/Sales-Representative/SalesRepresentativeFilter";
import SalesRepresentativeSummary from "../Components/Sales-Representative/SalesRepresentativeSummary";
import SalesRepresentativeTabs from "../Components/Sales-Representative/SalesRepresentativeTabs";

const SalesRepresentativ = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Sales Representative Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SalesRepresentativeFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SalesRepresentativeSummary/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SalesRepresentativeTabs/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default SalesRepresentativ;
