import React from "react";
import { Grid,TableFooter,TableRow,TableCell, 
        Table,TableHead,TableBody, TextField, InputAdornment } from "@material-ui/core";
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';
import RemoveOutlinedIcon from '@material-ui/icons/RemoveOutlined';
import AddOutlinedIcon from '@material-ui/icons/AddOutlined';
import PosContext from "../PosContext";

const InvoiceTableData = () => {
  return (
    <PosContext.Consumer>
        {context=>(
            <Table>
                <TableHead>
                <TableRow>
                    <TableCell>Product</TableCell>
                    <TableCell>Quantity</TableCell>
                    <TableCell>Price inc.tax</TableCell>
                    <TableCell>Subtotal</TableCell>
                    <TableCell>Action</TableCell>
                </TableRow>
                </TableHead>
                <TableBody>
                    {
                    context.invoiceItemList && context.invoiceItemList.map((item)=>{
                        return (
                        <TableRow key={item.id}>
                            <TableCell>{item.name}</TableCell>
                            <TableCell>
                                <TextField
                                    onChange = {(event)=>context.updateInvoiceProductData(item.id,null,true,event.target.value)}
                                    value={item.quantity}
                                    fullWidth
                                    variant="outlined"
                                    label="Qunatity"
                                    InputProps={{
                                        startAdornment: (
                                            <InputAdornment position="start">
                                                <RemoveOutlinedIcon 
                                                    onClick={()=>context.productQuantityDecrement(item.id)}
                                                    style={{cursor: "pointer" }}
                                                    color="error"/>
                                            </InputAdornment>
                                        ),
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <AddOutlinedIcon 
                                                    onClick={()=>context.productQuantityIncrement(item.id)}
                                                    style={{cursor: "pointer" }}
                                                    color="success"/>
                                            </InputAdornment>
                                        ),
                                    }}
                                    />
                            
                            </TableCell>
                            <TableCell>
                            <Grid container>
                                <TextField
                                    // onChange = {(event)=>context.updateInvoiceProductData(product.id,null,true,event.target.value)}
                                    value={item.price}
                                    fullWidth
                                    variant="outlined"
                                    label="Product Price"
                                />
                            </Grid>
                            </TableCell>
                            <TableCell>{item.subtotal}</TableCell>
                            <TableCell><DeleteOutlinedIcon onClick={()=>context.removeItem(item.id)} style={{color: "#e53935",cursor: "pointer"}}/></TableCell>
                        </TableRow>   
                        )
                    })
                    }
                </TableBody>
                <TableFooter>
                <TableRow>
                    <TableCell><strong>Total Item: {context.totalItem}</strong></TableCell>
                    <TableCell><strong>Gross Total Amount: BDT {context.grossTotal}</strong></TableCell>
                    <TableCell/>
                    <TableCell/>
                </TableRow>
                </TableFooter>
            </Table>
        )}
    </PosContext.Consumer>
  );
};

export default React.memo(InvoiceTableData);
