import React,{useState} from "react";
import { Card, Grid } from "@material-ui/core";
import { DataGrid } from '@material-ui/data-grid';
import { Autocomplete,TextField } from "@material-ui/core";
const registeredUserReportList = [
    {id: 1,date:"09/04/2021 20:33",closetime:"",user:"Mr.Super Admin <br/> superadmin@example.com",location: "Awesome Shop",totalcardslips:"0.00",totalcheques:"0.00",totalcash:"0.00",totalbanktransfer:"0.00",totaladvancepayment:"0.00",otherpayments:"0.00"},
    {id: 2,date:"09/04/2021 20:38",closetime:"",user:"Mr Admin <br/> admin@example.com",location: "Awesome Shop",totalcardslips:"0.00",totalcheques:"0.00",totalcash:"0.00",totalbanktransfer:"0.00",totaladvancepayment:"0.00",otherpayments:"0.00"},
]
const RegisterReportTable = () => {
    const columns = [
      { field: 'id', headerName: 'ID', width: 120 },
      {
        field: 'date',
        headerName: 'Date',
        width: 190,
        editable: true,
      },
      {
        field: 'closetime',
        headerName: 'Close Time',
        width: 220,
        editable: true,
      },
      {
        field: 'user',
        headerName: 'User',
        width: 190,
        editable: true,
      },
      {
        field: 'location',
        headerName: 'Location',
        width: 220,
        editable: true,
      },
      {
        field: 'totalcardslips',
        headerName: 'Total Card Slips',
        width: 190,
        editable: true,
      },
      {
        field: 'totalcheques',
        headerName: 'Total Cheques',
        width: 220,
        editable: true,
      },
      {
        field: 'totalcash',
        headerName: 'Total Cash',
        width: 190,
        editable: true,
      },
      {
        field: 'totalbanktransfer',
        headerName: 'Total Bank Transfer',
        width: 220,
        editable: true,
      },
      {
        field: 'totaladvancepayment',
        headerName: 'Total Advance Payment',
        width: 220,
        editable: true,
      },
      {
        field: 'otherpayments',
        headerName: 'Other Payments',
        width: 220,
        editable: true,
      },
    ];
  const onSelect = (event)=>{
    const {data} = event;
    const {id} = data;
    pushIdToSelectedList(id);
  }
  const [selectedIdList,setSelectedId] = useState([]);
  const pushIdToSelectedList = (id)=>{
      const includes = selectedIdList.includes(id);
      if(includes) {
        const updatedList = selectedIdList.filter((indexId)=> {return id!==indexId});
        setSelectedId(updatedList);
      }else{
          setSelectedId(prevIdList=>[...prevIdList,id]);
      }
  }
  return (
        <Card>
            <Grid 
            container
            direction="row"
            justifyContent="center"
            alignItems="center" 
            spacing="1">
            <Grid 
            style={{ marginBottom: "15px" }}
            item 
            lg={12} md={12}>
                <Grid container>
                    <Grid item lg={12} md={12}>
                        <Autocomplete
                            fullWidth
                            id="combo-box-demo"
                            options={registeredUserReportList}
                            getOptionLabel={(option) => option.name}
                            renderInput={(params) => <TextField  {...params} label="Search User" variant="outlined" />}
                            />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item lg={12} md={12}>
                <div style={{ height: 400, width: '100%' }}>
                    <DataGrid
                        rows={registeredUserReportList}
                        columns={columns}
                        pageSize={5}
                        checkboxSelection
                        onRowSelected={onSelect}
                    />
                </div>
            </Grid>
        </Grid>
        </Card>
  );
};

export default RegisterReportTable;
