import React from "react";
import { Button, Grid, Modal } from "@material-ui/core";
import PrintIcon from '@material-ui/icons/Print';
import { useState } from "react";
import PaymentIcon from '@material-ui/icons/Payment';
import InvoiceModal from "./InvoiceModal";
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
const POSFooter = ({setProcced,isProcced,items,checkOutOrder}) => {
    const [invoiceModalOpen,setInvoiceModalOpen] = useState(false);
    const openInvoiceModal = ()=>{
        setInvoiceModalOpen(!invoiceModalOpen);
    }
    const closeInvoiceModal = ()=>{
        setInvoiceModalOpen(!invoiceModalOpen);
    }
    const checkout = ()=>{
        MySwal.fire({
            title: 'Do you want to checkout the invoice?',
            showDenyButton: true,
            // showCancelButton: true,
            confirmButtonText: 'Procced',
            denyButtonText:'Stay'
          }).then(async (result) => {
            if (result.isConfirmed) {
                if(items.length === 0) displayError('Maybe you forget to include items!');
                else{
                    console.log('Called')
                    // await calculateGrandTotal();
                    setProcced(true);
                    // checkOutOrder();
                }
            } else if (result.isDenied) {}
          })
    }
    const displayError = (message)=>{
        MySwal.fire({
            icon: 'error',
            title: 'Oops...',
            text: `${message}`
          })
    }
  return (
    <Grid 
    container>
        <Grid item lg={12} md={12}>
            <Grid 
            container
            direction="row"
            spacing="2">
                {isProcced?(
                    <Grid item lg={4} md={4}>
                    <Button
                        fullWidth
                        variant="contained"
                        color="success"
                        startIcon={<PrintIcon/>}
                        onClick={openInvoiceModal}
                        >
                        Print Invoice
                    </Button>
                    <Modal
                        open={invoiceModalOpen}
                        onClose={closeInvoiceModal}
                        aria-labelledby="simple-modal-title"
                        aria-describedby="simple-modal-description"
                        >
                        <InvoiceModal 
                        closeModal={closeInvoiceModal}/>
                        </Modal>
                </Grid>
                ):(<>
                    <Grid item lg={12} md={12}>
                        <Button
                            fullWidth
                            variant="contained"
                            color="primary"
                            startIcon={<PaymentIcon/>}
                            onClick={checkout}
                            >
                            Checkout
                        </Button>
                    </Grid>
                </>)}
            </Grid>
        </Grid>
    </Grid>
  );
};

export default React.memo(POSFooter);
