import React from "react";
import { Grid } from "@material-ui/core";
import StockReportFilter from "../Components/Stock Report/StockReportFilter";
import StockReportClosing from "../Components/Stock Report/StockReportClosingStock";
import StockReportTable from "../Components/Stock Report/StockReportTable";

const StockReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Stock Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <StockReportFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <StockReportClosing/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <StockReportTable/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default StockReport;
