import React from "react";
import { Grid } from "@material-ui/core";
import CustomerGroupFilter from "../Components/Customer Group/CustomerGroupFilter";
import CustomerGroupTable from "../Components/Customer Group/CustomerGroupTable";

const CustomerGroupReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Customer Group Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <CustomerGroupFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <CustomerGroupTable/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default CustomerGroupReport;
