import { Card, InputLabel, Select } from '@material-ui/core';
import { Divider } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { TextField } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
const PurchaseAddPayment = ()=>{
    return (
        <>
            <Card>
                <CardContent>
                    <h2>Add Payment</h2>
                    <h3>Advance Balance: 0</h3>
                    <Grid 
                    container
                    spacing="2"
                    >
                        <Grid item lg={4} md={4}>
                            <TextField 
                                fullWidth
                                multiline
                                rows="6"
                                id="outlined-basic" 
                                label="Amount" 
                                variant="outlined" />
                        </Grid>
                        <Grid item lg={4} md={4}>
                            <TextField 
                                fullWidth
                                type="date"
                                id="outlined-basic" 
                                label="Paid On" 
                                variant="outlined" />
                        </Grid>
                        <Grid item lg={4} md={4}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Payment Method</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Payment Method"
                                >
                                <MenuItem value="">Please Select</MenuItem>
                                <MenuItem value="Cash">Cash</MenuItem>
                                <MenuItem value="Cheque">Cheque</MenuItem>
                                <MenuItem value="Bank Transfer">Bank Transfer</MenuItem>
                                <MenuItem value="Card">Card</MenuItem>
                                <MenuItem value="Advance">Advance</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>
                    <br />
                    <Divider/>
                    <br />
                    <Grid 
                    container
                    spacing="2">
                        <Grid item lg={4} md={4}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Payment Acount</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Payment Acount"
                                >
                                <MenuItem value="">Please Select</MenuItem>
                                <MenuItem value="None">None</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={8} md={8}/>
                    </Grid>
                    <br />
                    <Divider/>
                    <Grid container>
                        <Grid item lg={12} md={12}>
                            <TextField 
                                multiline
                                rows={5}
                                fullWidth
                                id="outlined-basic" 
                                label="Payment Note" 
                                variant="outlined" />
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </>
    )
}

export default PurchaseAddPayment;