import React from "react";
import { TextField, Grid } from "@material-ui/core";
import PosContext from "../../PosContext";
const PaymentAmountStatus = () => {
  return (
    <PosContext.Consumer>
        {context=>(
            <Grid 
            container
            direction="cloumn">
                <Grid item lg={12} md={12}>
                    <Grid 
                    container
                    direction="row"
                    spacing="1">
                        <Grid item lg={6} md={12}>
                            <h2>Total Amount</h2>
                        </Grid>
                        <Grid item lg={6} md={12}>
                            <TextField 
                            fullWidth
                            value={context.grandTotal}
                            disabled
                            type="number"
                            label="Total Amount" 
                            variant="outlined" />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Grid 
                    container
                    direction="row"
                    spacing="1">
                        <Grid item lg={6} md={12}>
                            <h2>Paid Amount</h2>
                        </Grid>
                        <Grid item lg={6} md={12}>
                            <TextField 
                            fullWidth
                            disabled
                            value={context.paidAmount}
                            label="Paid Amount" 
                            variant="outlined" />
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        )}
    </PosContext.Consumer>
    
  );
}
export default PaymentAmountStatus;
