import React,{useState} from "react";
import { Grid,Button,MenuItem,Menu, Card } from "@material-ui/core";
import { DataGrid } from '@material-ui/data-grid';
import EditIcon from '@material-ui/icons/Edit';
import { Divider,Autocomplete,TextField } from "@material-ui/core";
import { Modal } from "@material-ui/core";
import AddBoxIcon from '@material-ui/icons/AddBox';
import { useNavigate } from "react-router-dom";
// import ViewProductModal from "./Sub-Components/ViewProductModal";
export const saleList = [
  {id: 1,date: new Date(),invoiceno:"AS0004",customername:"Walk-In Customer",contactnumber:"(378)400-1234",
  location:"Awesome Shop",paymentstatus:"Paid",paymentmethod:"Cash",totalamount:"750.00",totalpaid:"750.00",
  selldue:"0.00", sellreturndue:"",shippingstatus:"",totalitems:"",addedby:"",
    sellnote:"",staffnote:"",shippingdetails:""},
  
]
const SalesTable = () => {
  const navigate = useNavigate();
    const columns = [
      { field: 'id', headerName: 'ID', width: 120 },
      {
        field: 'date',
        headerName: 'Date',
        width: 190,
        editable: true,
      },
      {
        field: 'invoiceno',
        headerName: 'Invoice No.',
        width: 220,
        editable: true,
      },
      {
        field: 'customername',
        headerName: 'Customer Name',
        width: 220,
        editable: true,
      },
      {
        field: 'contactnumber',
        headerName: 'Contact Number',
        width: 190,
        editable: true,
      },
      {
        field: 'location',
        headerName: 'Location',
        width: 190,
        editable: true,
      },
      {
        field: 'paymentstatus',
        headerName: 'Payment Status',
        width: 190,
        editable: true,
      },
      {
        field: 'paymentmethod',
        headerName: 'Payment Method',
        width: 190,
        editable: true,
      },
      {
        field: 'totalamount',
        headerName: 'Total Amount',
        width: 190,
        editable: true,
      },
      {
        field: 'totalpaid',
        headerName: 'Total Paid',
        width: 190,
        editable: true,
      },
      {
        field: 'selldue',
        headerName: 'Sell Due',
        width: 190,
        editable: true,
      },
      {
        field: 'sellreturndue',
        headerName: 'Sell Return Due',
        width: 190,
        editable: true,
      },
      {
        field: 'shippingstatus',
        headerName: 'Shipping Status',
        width: 190,
        editable: true,
      },
      {
        field: 'totalitems',
        headerName: 'Total Items',
        width: 190,
        editable: true,
      },
      {
        field: 'addedby',
        headerName: 'Added By',
        width: 190,
        editable: true,
      },
      {
        field: 'sellnote',
        headerName: 'Sell Note',
        width: 190,
        editable: true,
      },
      {
        field: 'staffnote',
        headerName: 'Staff Note',
        width: 190,
        editable: true,
      },
      {
        field: 'shippingdetails',
        headerName: 'Shipping Details',
        width: 190,
        editable: true,
      },
    ];
  const onSelect = (event)=>{
    const {data} = event;
    const {id} = data;
    pushIdToSelectedList(id);
  }
  const [selectedIdList,setSelectedId] = useState([]);
  const pushIdToSelectedList = (id)=>{
      const includes = selectedIdList.includes(id);
      if(includes) {
        const updatedList = selectedIdList.filter((indexId)=> {return id!==indexId});
        setSelectedId(updatedList);
      }else{
          setSelectedId(prevIdList=>[...prevIdList,id]);
      }
  }
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const [viewProductModalOpen,setViewProductModalOpen] = useState(false);
  const closeViewProductModal = ()=>{
    setViewProductModalOpen(false);
  }
  const openViewProductModalOpen = ()=>{
    setViewProductModalOpen(true);
  }
  return (
      <Card>
          <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid container >
                <Grid  item lg={3} md={3}>
                    <Button
                    aria-controls="simple-menu" 
                    aria-haspopup="true"
                    onClick={handleMenu}
                    
                    color="success" 
                    variant="contained"
                    startIcon={<EditIcon/>}
                    >
                        Actions
                    </Button>
                        <Menu
                            // style={{ width: "550px" }}
                            id="dd-menu"
                            anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                            anchorOrigin={{ horizontal: "left", vertical: "bottom" }}
                            transformOrigin={{ horizontal: "left", vertical: "top" }}
                            >
                            <MenuItem onClick={openViewProductModalOpen}>
                                View
                            </MenuItem>
                            <MenuItem onClick={handleClose}>Edit</MenuItem>
                            <MenuItem onClick={handleClose}>Delete</MenuItem>
                            <MenuItem onClick={handleClose}>Edit Shipping</MenuItem>
                            <MenuItem onClick={handleClose}>Print Invoice</MenuItem>
                            <MenuItem onClick={handleClose}>Packing Slip</MenuItem>
                            <Divider/>
                            <MenuItem onClick={handleClose}>View Payments</MenuItem>
                            <MenuItem onClick={handleClose}>Duplicate Sell</MenuItem>
                            <MenuItem onClick={handleClose}>Sell Return</MenuItem>
                            <MenuItem onClick={handleClose}>Invoice URL</MenuItem>
                        </Menu>
                </Grid>
                {/* View product modal */}
                <Modal
                    open={viewProductModalOpen}
                    onClose={closeViewProductModal}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    >
                    {/* <ViewProductModal 
                        closeModal={closeViewProductModal}/> */}
                </Modal>
                <Grid item lg={3} md={3}>
                    <Button
                        onClick={()=>{return navigate('/sell/create')}}
                        aria-controls="simple-menu" 
                        aria-haspopup="true"
                        color="primary" 
                        variant="contained"
                        startIcon={<AddBoxIcon/>}
                        >
                            Add
                        </Button>
                </Grid>
                <Grid item lg={6} md={6}/>
            </Grid>
            <br />
            <Grid container>
                <Grid item lg={12} md={12}>
                    <Autocomplete
                        fullWidth
                        id="combo-box-demo"
                        options={saleList}
                        getOptionLabel={(option) => option.productname}
                        renderInput={(params) => <TextField  {...params} label="Search Supplier" variant="outlined" />}
                        />
                </Grid>
            </Grid>
        </Grid>
        <Grid item lg={12} md={12}>
            <div style={{ height: 400, width: '100%' }}>
                <DataGrid
                    rows={saleList}
                    columns={columns}
                    pageSize={5}
                    checkboxSelection
                    onRowSelected={onSelect}
                />
            </div>
        </Grid>
      </Grid>
      </Card>
  );
};

export default SalesTable;
