import React from "react";
import { Card, Grid,Button } from "@material-ui/core";
import PosContext from "../PosContext";
import InvoiceTableData from "./InvoiceTableData";
import POSFooter from "./POSFooter";
import InvoiceItemList from "./InvoiceItemList";
const TableCard = () => {
  return (
    <PosContext.Consumer>
        {context=>(
            <Card>
            <Grid 
            container
            direction="row"
            spacing="2">
            {context.proccedToCheckout?
            (<>
            <Grid 
            style={{ marginBottom:"15px" }}
            item lg={4} md={4}>
                <Button
                onClick={()=>context.setProccedToCheckout(false)}
                fullWidth
                color="success"
                variant="contained">
                    New Order
                </Button>
            </Grid>
            </>):(<div></div>)}
            <br />
            <Grid item lg={12} md={12}>
                {context.proccedToCheckout === true ?(<InvoiceItemList/>):(<InvoiceTableData/>)}
            </Grid>
            </Grid>
            <br />
            <Grid item lg={12} md={12} sm={12}>
                <POSFooter 
                    // calculateGrandTotal = {context.calculateGrandTotal}
                    items={context.invoiceItemList}
                    setProcced={context.setProccedToCheckout} 
                    checkOutOrder={context.checkOutOrder}
                    isProcced={context.proccedToCheckout}/>
            </Grid>
        </Card>
        )}
    </PosContext.Consumer>
  );
};

export default React.memo(TableCard);
