import React from "react";
import {Card,CardContent,Divider,Grid,TextField, Button, Select } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import SaveOutlinedIcon from '@material-ui/icons/SaveOutlined';
import { Checkbox } from "@material-ui/core";
import { useState } from "react";
import { FormControlLabel } from "@material-ui/core";
import { FormControl } from "@material-ui/core";
import { InputLabel } from "@material-ui/core";
import { MenuItem } from "@material-ui/core";
const AddSupplierModal = ({closeModal}) => {
    const [individualCheckbox,setIndividualCheckbox] = useState();
    const [businessCheckbox,setBusinessCheckbox] = useState();
    const [prefix,setPrefix] = useState();
  return (
    <>
      <Grid container>
        <Grid item lg={3} md={3}/>
        <Grid item lg={6} md={6}>
        <Card>
            <CardContent>
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                  <Grid item lg={6} md={6}>
                    <h1>Add Supplier</h1>
                  </Grid>
                  <Grid item lg={3} md={3}/>
                  <Grid item lg={2} md={2}/>
                  <Grid item lg={1} md={1}>
                    <h5><CloseIcon color="error" onClick={closeModal} style={{cursor:"pointer"}}/></h5>
                  </Grid>
                </Grid>
            </CardContent>
            <Divider/>
            <CardContent>
            <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={4} md={4}>
                        <FormControlLabel
                            control={<Checkbox checked={individualCheckbox} 
                            onChange={()=>{setIndividualCheckbox(!individualCheckbox)}} name="checkedA" />}
                            label="Individual"
                        />
                    </Grid>
                    <Grid item lg={4} md={4}>
                        <FormControlLabel
                                control={<Checkbox checked={businessCheckbox} 
                                onChange={()=>{setBusinessCheckbox(!businessCheckbox)}} name="checkedA" />}
                                label="Business"
                            />
                    </Grid>
                    <Grid item lg={4} md={4}/>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    {individualCheckbox ? (
                    <>
                    <Grid item lg={3} md={3}>
                        <FormControl 
                        fullWidth
                        variant="outlined" >
                            <InputLabel id="demo-simple-select-filled-label">Prefix</InputLabel>
                            <Select
                            labelId="demo-simple-select-filled-label"
                            id="demo-simple-select-filled"
                            label="Prefix"
                            value={prefix}
                            onChange={(event)=>{setPrefix(event.target.value)}}
                            >
                            <MenuItem value="">
                                <em>None</em>
                            </MenuItem>
                            <MenuItem value="Mr.">Mr.</MenuItem>
                            <MenuItem value="Mrs.">Mrs.</MenuItem>
                            <MenuItem value="Ms.">Ms.</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item lg={3} md={3}>
                      <TextField
                        variant="outlined"
                        label="First Name"
                      />
                    </Grid>
                    <Grid item lg={3} md={3}>
                      <TextField
                          variant="outlined"
                          label="Middle Name"
                        />
                    </Grid>
                    <Grid item lg={3} md={3}>
                      <TextField
                            variant="outlined"
                            label="Last Name"
                          />
                    </Grid>
                    </>):(
                        <>
                            <Grid item lg={3} md={3}>
                                <TextField
                                    variant="outlined"
                                    label="Business Name"
                                />
                            </Grid>
                            <Grid item lg={3} md={3}/>
                            <Grid item lg={3} md={3}/>
                            <Grid item lg={3} md={3}/>

                        </>
                    )}
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={4} md={4}>
                      <TextField
                        type="number"
                        variant="outlined"
                        label="Mobile"
                      />
                    </Grid>
                    <Grid item lg={4} md={4}>
                      <TextField
                          type="number"
                          variant="outlined"
                          label="Alt Contact"
                        />
                    </Grid>
                    <Grid item lg={4} md={4}/>

                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={4} md={4}>
                      <TextField
                          type="number"
                          variant="outlined"
                          label="Landline"
                        />
                    </Grid>
                    <Grid item lg={4} md={4}>
                      <TextField
                        type="email"
                        variant="outlined"
                        label="Email"
                      />
                    </Grid>
                    {individualCheckbox ? (
                        <>
                            <Grid item lg={4} md={4}>
                                <TextField
                                    fullWidth
                                    type="date"
                                    variant="outlined"
                                    label="Date Of Birth"
                                    />
                            </Grid>
                        </>
                    ):(<Grid item lg={4} md={4}/>)}
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={6} md={6}>
                      <TextField
                        fullWidth
                          multiline
                          rows={4}
                          variant="outlined"
                          label="Address 1"
                        />
                    </Grid>
                    <Grid item lg={6} md={6}>
                      <TextField
                        fullWidth
                        multiline
                        rows={4}
                        variant="outlined"
                        label="Address 2"
                      />
                    </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={6} md={6}>
                      <TextField
                        fullWidth
                          variant="outlined"
                          label="City"
                        />
                    </Grid>
                    <Grid item lg={6} md={6}>
                      <TextField
                        fullWidth
                        variant="outlined"
                        label="State"
                      />
                    </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={6} md={6}>
                      <TextField
                        fullWidth
                          variant="outlined"
                          label="Country"
                        />
                    </Grid>
                    <Grid item lg={6} md={6}>
                      <TextField
                        fullWidth
                        type="number"
                        variant="outlined"
                        label="Zip"
                      />
                    </Grid>
                </Grid>
                <br />
                <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={3} md={3}>
                        <TextField
                            fullWidth
                            type="number"
                            variant="outlined"
                            label="Tax Number"
                            />
                    </Grid>
                    <Grid item lg={3} md={3}>
                        <TextField
                            fullWidth
                            type="number"
                            variant="outlined"
                            label="Opening Balance"
                            />
                    </Grid>
                    <Grid item lg={3} md={3}>
                        <FormControl 
                            fullWidth
                            variant="outlined" >
                                <InputLabel id="demo-simple-select-filled-label">Pay Term Type</InputLabel>
                                <Select
                                labelId="demo-simple-select-filled-label"
                                id="demo-simple-select-filled"
                                label="Pay Term Type"
                                // value={prefix}
                                // onChange={(event)=>{setPrefix(event.target.value)}}
                                >
                                <MenuItem value="">
                                    <em>Please Select</em>
                                </MenuItem>
                                <MenuItem value="Days">Days</MenuItem>
                                <MenuItem value="Months">Months</MenuItem>
                                </Select>
                        </FormControl>
                    </Grid>
                    <Grid item lg={3} md={3}>
                        <TextField
                            fullWidth
                            type="number"
                            variant="outlined"
                            label="Pay Term Value"
                            />
                    </Grid>
                </Grid>
            </CardContent>
            <Divider/>
            <CardContent>
                <Grid 
                container
                direction="row"
                justifyContent="center"
                alignItems="center" 
                spacing="1">
                  <Grid item lg={12} md={12}>
                      <Button
                        fullWidth
                        variant="contained"
                        color="success"
                        endIcon={<SaveOutlinedIcon/>}
                      >
                        Save
                      </Button>
                  </Grid>
                </Grid>
            </CardContent>
        </Card>
        </Grid>
        <Grid item lg={3} md={3}/>
      </Grid>
  </>
  );
};

export default AddSupplierModal;
