import React from "react";
import { Grid } from "@material-ui/core";
import PaymentAmountCalculatorNumbers from "./PaymentAmountCalculatorNumbers";
import PaymentAmountCalculatorNotes from "./PaymentAmountCalculatorNotes";
import PaymentAmountCalculatorOperations from "./PaymentAmountCalculatorOperations";
const PaymentAmountCalculator = () => {
  return (
    <Grid 
        container
        direction="row"
        spacing="1">
            <Grid item lg={2} md={12}>
                <PaymentAmountCalculatorNotes/>
            </Grid>
            <Grid item lg={8} md={12}>
                <Grid 
                container
                direction="row"
                spacing="1">
                    <PaymentAmountCalculatorNumbers/>
                </Grid>
            </Grid>
            <Grid item lg={2} md={12}>
                <PaymentAmountCalculatorOperations/>
            </Grid>
        </Grid>
  );
}
export default PaymentAmountCalculator;
