import React from "react";
import { Grid, Button } from "@material-ui/core";
import PosContext from "../PosContext";
const FoodTypeList = ({changeCategory}) => {
  return (
    <PosContext.Consumer>
      {context=>(
        <Grid 
        style={{ padding: "18px" }}
        container
        direction="column"
        spacing="3">
            <div style={{ height:"540px",overflowY: "auto" }}>
                <Grid 
                  style={{ paddingTop:"8px" }}
                  item lg={12} md={12}>
                      <Button 
                          onClick={()=>changeCategory("All")}
                          style={{ background: '#ff1744'}}
                          fullWidth
                          variant="contained">
                              All
                      </Button>
                  </Grid>
                {context.categoryList.map((category,index)=>{
                        return (
                        <Grid 
                        style={{ paddingTop:"8px" }}
                        item lg={12} md={12}>
                            <Button 
                                onClick={()=>changeCategory(category.name)}
                                style={{ background: index%2===0 ? '#ff1744': index%3===0 ? '#2196f3': '#f50057' }}
                                fullWidth
                                variant="contained">
                                    {category.name}
                            </Button>
                        </Grid>
                        )
                    })}
            </div>
        </Grid>
      )}
    </PosContext.Consumer>
  );
};

export default FoodTypeList;
