import React from "react";
import { Grid,Button } from "@material-ui/core";
import PosContext from "../../PosContext";
const PaymentAmountCalculatorNotes = () => {
    const buttonStyles = {
        padding:"18px 0",
        fontSize:"20px"
    }
    const notes = [10,20,50,100,200,500,1000];
  return (
    <PosContext.Consumer>
        {context=>(
            <Grid 
            container
            direction="column"
            spacing="1">
                {notes.map((note,index)=>{
                    return (
                        <Grid key={index}
                        item lg={12} md={12}>
                            <Button
                            style={buttonStyles}
                            fullWidth
                            onClick={()=>context.setPaidAmount(note)}
                            color="success"
                            variant="contained">
                                {note}
                            </Button>
                        </Grid>
                    )
                })}
        </Grid>
        )}
    </PosContext.Consumer>
  );
}
export default PaymentAmountCalculatorNotes;
