import React from "react";
import {Card,CardContent,Divider,Grid, Button, FormControl, InputLabel, Select, MenuItem } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';
import PosContext from "../PosContext";
const SelectPaymentTypeModal = ({closeModal}) => {
  return (
    <PosContext.Consumer>
      {context=>(
        <Grid container>
          <Grid item lg={3} md={3}/>
          <Grid item lg={6} md={6}>
          <Card>
              <CardContent>
                  <Grid 
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center" 
                    spacing="2">
                    <Grid item lg={6} md={6}>
                      <h1>Select Payment Type</h1>
                    </Grid>
                    <Grid item lg={3} md={3}/>
                    <Grid item lg={2} md={2}/>
                    <Grid item lg={1} md={1}>
                      <h5><CloseIcon color="error" onClick={closeModal} style={{cursor:"pointer"}}/></h5>
                    </Grid>
                  </Grid>
              </CardContent>
              <Divider/>
              <CardContent>
                    <Grid 
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center" 
                    spacing="1">
                      <Grid item lg={12} md={12}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Payment Type</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={context.paymentType}
                                label="Payment Type"
                                onChange={(event)=>context.changePaymentType(event.target.value)}
                            >
                                <MenuItem value="Cash">Cash</MenuItem>
                                <MenuItem value="Visa Card">Visa Card</MenuItem>
                                <MenuItem value="Master Card">Master Card</MenuItem>
                                <MenuItem value="Amex Card">Amex Card</MenuItem>
                            </Select>
                            </FormControl>
                      </Grid>
                  </Grid>
              </CardContent>
              <Divider/>
              <CardContent>
                  <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={4} md={4}/>
                    <Grid item lg={3} md={3}/>
                    <Grid item lg={2} md={2}/>
                    <Grid item lg={1} md={1}/>
                    <Grid item lg={2} md={2}>
                        <Button
                            onClick={closeModal}
                            variant="contained"
                            color="error"
                            endIcon={<CloseOutlinedIcon/>}
                          >
                            Close
                          </Button>
                    </Grid>
                  </Grid>
              </CardContent>
          </Card>
          </Grid>
          <Grid item lg={3} md={3}/>
        </Grid>
      )}
  </PosContext.Consumer>
  );
};

export default SelectPaymentTypeModal;
