import React,{useState} from "react";
import { Grid,Button,Menu,
  MenuItem } from "@material-ui/core";
import { DataGrid } from '@material-ui/data-grid';
import EditIcon from '@material-ui/icons/Edit';
import { useNavigate } from "react-router-dom";
import AddBoxIcon from '@material-ui/icons/AddBox';
import AddCustomerModal from "../POS/components/AddCustomerModal";
import { Modal } from "@material-ui/core";
const Customers = () => {
    const userList = [
        {id: 1,role: "Admin"},
        {id: 2,role: "Cashier"}
    ]
    const columns = [
      { field: 'id', headerName: 'ID', width: 120 },
      {
        field: 'role',
        headerName: 'Role',
        width: 150,
        editable: true,
      }
    ];
  const onSelect = (event)=>{
    const {data} = event;
    const {id} = data;
    pushIdToSelectedList(id);
  }
  const [selectedIdList,setSelectedId] = useState([]);
  const pushIdToSelectedList = (id)=>{
      const includes = selectedIdList.includes(id);
      if(includes) {
        const updatedList = selectedIdList.filter((indexId)=> {return id!==indexId});
        setSelectedId(updatedList);
      }else{
          setSelectedId(prevIdList=>[...prevIdList,id]);
      }
  }
  const [anchorEl, setAnchorEl] = React.useState(null);
  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const navigate = useNavigate();
  const [addCustomerModalOpen,setAddCustomerModalOpen] = useState(false);
  const closeCustomerModal = ()=>{
    setAddCustomerModalOpen(false);
  }
  const openAddCustomerModalOpen = ()=>{
    setAddCustomerModalOpen(true);
  }
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid container >
              <Grid  item lg={3} md={3}>
                    <Button
                    aria-controls="simple-menu" 
                    aria-haspopup="true"
                    onClick={handleMenu}
                    color="success" 
                    variant="contained"
                    startIcon={<EditIcon/>}
                    >
                        Actions
                    </Button>
                        <Menu
                            // style={{ width: "550px" }}
                            id="dd-menu"
                            anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                            anchorOrigin={{ horizontal: "left", vertical: "bottom" }}
                            transformOrigin={{ horizontal: "left", vertical: "top" }}
                            >
                            <MenuItem onClick={handleClose}>Pay</MenuItem>
                            <MenuItem onClick={()=>{navigate(`/customer/${selectedIdList[0]}`)}}>View</MenuItem>
                            <MenuItem onClick={handleClose}>Edit</MenuItem>
                            <MenuItem onClick={handleClose}>Deleted</MenuItem>
                            <MenuItem onClick={handleClose}>Deactivate</MenuItem>
                        </Menu>
                </Grid>
                <Grid item lg={3} md={3}>
                <Button
                    aria-controls="simple-menu" 
                    aria-haspopup="true"
                    color="primary" 
                    variant="contained"
                    startIcon={<AddBoxIcon/>}
                    onClick={openAddCustomerModalOpen}
                    >
                        Add
                    </Button>
                    <Modal
                            open={addCustomerModalOpen}
                            onClose={closeCustomerModal}
                            aria-labelledby="simple-modal-title"
                            aria-describedby="simple-modal-description"
                            >
                            <AddCustomerModal 
                            closeModal={closeCustomerModal}/>
                        </Modal>
                </Grid>
            </Grid>
        </Grid>
        <Grid item lg={12} md={12}>
            <div style={{ height: 400, width: '100%' }}>
                <DataGrid
                    rows={userList}
                    columns={columns}
                    pageSize={5}
                    checkboxSelection
                    onRowSelected={onSelect}
                />
            </div>
        </Grid>
      </Grid>
  );
};

export default Customers;
