import { Card, CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
const DashboardCards = ()=>{
    return (
        <>
            <Grid 
            container
            spacing="2">
                <Grid item lg={3} md={3}>
                    <Card>
                        <CardContent>
                            <h5>TOTAL PURCHASE</h5>
                            <h5>$ 235,656.00</h5>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item lg={3} md={3}>
                    <Card>
                        <CardContent>
                            <h5>TOTAL SALES</h5>
                            <h5>$ 235,656.00</h5>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item lg={3} md={3}>
                    <Card>
                        <CardContent>
                            <h5>PURCHASE DUE</h5>
                            <h5>$ 235,656.00</h5>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item lg={3} md={3}>
                    <Card>
                        <CardContent>
                            <h5>INVOICE DUE</h5>
                            <h5>$ 235,656.00</h5>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            <br />
            <Grid 
            container
            spacing="2">
                <Grid item lg={3} md={3}>
                    <Card>
                        <CardContent>
                            <h5>EXPENSE</h5>
                            <h5>$ 235,656.00</h5>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item lg={9} md={9}/>
            </Grid>
        </>
    )
}

export default DashboardCards;