import React from "react";
import { Grid } from "@material-ui/core";
import PurchaseAndSaleFilter from "../Components/Purchas And Sale/PurchaseAndSaleFilter";
import PurchaseAndSaleOverall from "../Components/Purchas And Sale/PurchaseAndSaleOverall";
import PurchaseAndSalePurchases from "../Components/Purchas And Sale/PurchaseAndSalePurchases";

const PurchaseAndSaleReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Purchase And Sale Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <PurchaseAndSaleFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <PurchaseAndSalePurchases/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <PurchaseAndSaleOverall/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default PurchaseAndSaleReport;
