import React, { useState } from "react";
import { Grid, Button, Modal } from "@material-ui/core";
import PrintIcon from '@material-ui/icons/Print';
import AddCustomerModal from "./AddCustomerModal";
import AddCustomerTableModal from "./AddCustomerTable";
import AddWaiterModal from "./AddWaiterModal";
import AddGuestNumberModal from "./AddGuestNumberModal";
const PosWorkList = () => {
    const [addCustomerModal,setAddCustomerModalOpen] = useState(false);
    const openAddCustomerModal = ()=>{
        setAddCustomerModalOpen(true);
    }
    const closeAddCustomerModal = ()=>{
        setAddCustomerModalOpen(false);
    }
    const [addCustomerTableModal,setAddCustomerTableModalOpen] = useState(false);
    const openAddCustomerTableModal = ()=>{
        setAddCustomerTableModalOpen(true);
    }
    const closeAddCustomerTableModal = ()=>{
        setAddCustomerTableModalOpen(false);
    }
    const [addWaiterModal,setAddWaiterModalOpen] = useState(false);
    const openAddWaiterModal = ()=>{
        setAddWaiterModalOpen(true);
    }
    const closeAddWaiterModal = ()=>{
        setAddWaiterModalOpen(false);
    }
    const [guestNumberModal,setGuestNumberModalOpen] = useState(false);
    const openGuestNumberModal = ()=>{
        setGuestNumberModalOpen(true);
    }
    const closeGuestNumberModal = ()=>{
        setGuestNumberModalOpen(false);
    }
  return (
        <Grid 
        style={{ padding: "18px" }}
        container
        direction="column"
        spacing="2">
            <Grid item lg={12} md={12}>
                <Button 
                fullWidth
                onClick={openAddCustomerTableModal}
                variant="outlined">
                    Change Table
                </Button>
                <Modal
                    open={addCustomerTableModal}
                    onClose={closeAddCustomerTableModal}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    >
                    <AddCustomerTableModal 
                    closeModal={closeAddCustomerTableModal}/>
                </Modal>
            </Grid>
            <Grid item lg={12} md={12}>
                <Button 
                fullWidth
                    onClick={openAddCustomerModal}
                    variant="outlined">
                        Select Customer
                </Button>
                <Modal
                    open={addCustomerModal}
                    onClose={closeAddCustomerModal}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    >
                    <AddCustomerModal 
                    closeModal={closeAddCustomerModal}/>
                </Modal>
            </Grid>
            <Grid item lg={12} md={12}>
                <Button 
                fullWidth
                onClick={openAddWaiterModal}
                variant="outlined">
                    Change Waiter
                </Button>
                <Modal
                    open={addWaiterModal}
                    onClose={closeAddWaiterModal}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    >
                    <AddWaiterModal 
                    closeModal={closeAddWaiterModal}/>
                </Modal>
            </Grid>
            <Grid item lg={12} md={12}>
                <Button 
                fullWidth
                onClick={openGuestNumberModal}
                variant="outlined">
                    Number Of Guests
                </Button>
                <Modal
                    open={guestNumberModal}
                    onClose={closeGuestNumberModal}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    >
                    <AddGuestNumberModal 
                    closeModal={closeGuestNumberModal}/>
                </Modal>
            </Grid>
            <Grid item lg={12} md={12}>
                <Button 
                fullWidth
                startIcon={<PrintIcon/>}
                variant="contained">
                    Print Bill
                </Button>
            </Grid>
        </Grid>
  );
};

export default PosWorkList;
