import React from "react";
import { Grid } from "@material-ui/core";
import TaxReportTabs from "../Components/Tax Report/TaxReportTabs";
import TaxReportFilter from "../Components/Tax Report/TaxReportFilter";
import TaxReportOverall from "../Components/Tax Report/TaxReportOverall";

const TaxReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Tax Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <TaxReportFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <TaxReportOverall/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <TaxReportTabs/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default TaxReport;
