import React from "react";
import { Card, Grid } from "@material-ui/core";
import PosContext from "../PosContext";
const ItemCard = () => {
  return (
    <PosContext.Consumer>
        {context=>(
            <Grid 
            style={{ padding: "18px" }}
            container>
                <Grid item lg={12} md={12}>
                    <Grid item container lg={12} md={12}>
                    {context.productList.map((product)=>{
                        return (
                        <Grid item key={product.id} lg={4} md={4}>   
                            <Card 
                            onClick={()=>context.onAddItem(product)}
                            style={{ borderRadius: "5px",cursor: "pointer",textAlign:"center",
                            background: "#2196f3",color:"#fff" }}>
                                <Grid 
                                item lg={12} md={12}>
                                    <h4>{product.name}</h4>
                                </Grid>
                            </Card>
                        </Grid>)
                    })}
                    </Grid>
                </Grid>
            </Grid>
        )}
    </PosContext.Consumer>
  );
};

export default React.memo(ItemCard);
