import { Autocomplete, Card, InputLabel, Select } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { TextField } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
import customerList from '../../POS/customerList';
const SellReturnFilter = ()=>{
    return (
        <>
            <Card>
                <CardContent>
                    <Grid 
                    container
                    spacing="2">
                        <Grid item lg={3} md={3}>
                        <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Business Location</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Business Location"
                                >
                                <MenuItem value="All">All</MenuItem>
                                <MenuItem value="Awesome Shop">Awesome Shop</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={3} md={3}>
                            <Autocomplete
                            fullWidth
                            id="combo-box-demo"
                            options={customerList}
                            getOptionLabel={(option) => option.name}
                            renderInput={(params) => <TextField  {...params} label="Customer" variant="outlined" />}
                            />
                        </Grid>
                        <Grid item lg={3} md={3}>
                            <Autocomplete
                                fullWidth
                                id="combo-box-demo"
                                options={customerList}
                                getOptionLabel={(option) => option.name}
                                renderInput={(params) => <TextField  {...params} label="User" variant="outlined" />}
                                />
                        </Grid>
                        <Grid item lg={3} md={3}>
                            <Grid 
                            container
                            spacing="2">
                                <Grid item lg={6} md={6}>
                                    <TextField 
                                        fullWidth
                                        type="date"
                                        id="outlined-basic" 
                                        label="Start Date" 
                                        variant="outlined" />
                                </Grid>
                                <Grid item lg={6} md={6}>
                                    <TextField 
                                        fullWidth
                                        type="date"
                                        id="outlined-basic" 
                                        label="End Date" 
                                        variant="outlined" />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </>
    )
}

export default SellReturnFilter;