import { Card, InputLabel, Select } from '@material-ui/core';
import { Divider } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { TextField } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
const PurchaseDiscount = ()=>{
    return (
        <>
            <Card>
                <CardContent>
                    <Grid container>
                        <Grid item lg={3} md={3}>
                                <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Discount Type</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Discount Type"
                                    >
                                    <MenuItem value="None">None</MenuItem>
                                    <MenuItem value="Fixed">Fixed</MenuItem>
                                    <MenuItem value="Percentage">Percentage</MenuItem>
                                    </Select>
                                </FormControl>
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={3} md={3}>
                            <TextField 
                                fullWidth
                                id="outlined-basic" 
                                label="Discount Amount" 
                                variant="outlined" />
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={3} md={3}>
                            <p><strong>Discount(-): 0.00</strong></p>
                        </Grid>
                    </Grid>
                    <br />
                    <Divider/>
                    <br />
                    <Grid container>
                        <Grid item lg={3} md={3}>
                            <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Purchase Tax</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Purchase Tax"
                                    >
                                    <MenuItem value="None">None</MenuItem>
                                    <MenuItem value="Vat@10%">Vat@10%</MenuItem>
                                    <MenuItem value="CGST@10%">CGST@10%</MenuItem>
                                    <MenuItem value="SGST@8%">SGST@8%</MenuItem>
                                    <MenuItem value="GST@18%">GST@18%</MenuItem>
                                    </Select>
                                </FormControl>
                        </Grid>
                        <Grid item lg={5} md={5}/>
                        <Grid item lg={3} md={3}>
                            <Grid 
                            container
                            direction="row">
                                <Grid>
                                    <p><strong>Purchase Tax(+): 0.00</strong></p>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <br />
                    <Divider/>
                    <Grid container>
                        <Grid item lg={12} md={12}>
                            <TextField 
                                fullWidth
                                multiline
                                rows={6}
                                id="outlined-basic" 
                                label="Additional Notes" 
                                variant="outlined" />
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </>
    )
}

export default PurchaseDiscount;