import React, { useState } from "react";
import {Card,CardContent,Divider,Grid,TextField, Button, Autocomplete, FormControl, FormLabel, RadioGroup, FormControlLabel, Radio } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import SaveOutlinedIcon from '@material-ui/icons/SaveOutlined';
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';
import PosContext from "../PosContext";
const customerList = [
  {id: 1,name:"John",phone:"+8801753621025"},
  {id: 2,name:"Doe",phone:"+8801753621025"},
  {id: 3,name:"Alex",phone:"+8801753621025"},
  {id: 4,name:"Alexa",phone:"+8801753621025"},
  {id: 5,name:"Siri",phone:"+8801753621025"},
  {id: 6,name:"Bill",phone:"+8801753621025"},
  {id: 7,name:"Gates",phone:"+8801753621025"},
  {id: 8,name:"Elon",phone:"+8801753621025"},
  {id: 9,name:"Jeff",phone:"+8801753621025"},
  {id: 10,name:"Bezos",phone:"+8801753621025"},
]
const AddCustomerModal = ({closeModal}) => {
  const [customerExist,setCustomerExist] = useState('yes');
  return (
    <PosContext.Consumer>
      {context=>(
        <Grid container>
          <Grid item lg={3} md={3}/>
          <Grid item lg={6} md={6}>
          <Card>
              <CardContent>
                  <Grid 
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center" 
                    spacing="2">
                    <Grid item lg={6} md={6}>
                      <h1>Customer</h1>
                    </Grid>
                    <Grid item lg={3} md={3}/>
                    <Grid item lg={2} md={2}/>
                    <Grid item lg={1} md={1}>
                      <h5><CloseIcon color="error" onClick={closeModal} style={{cursor:"pointer"}}/></h5>
                    </Grid>
                  </Grid>
              </CardContent>
              <Divider/>
              <CardContent>
                  <Grid
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                  <Grid item lg={12} md={12}>
                      <FormControl component="fieldset">
                        <FormLabel 
                        component="legend">Customer Exists?</FormLabel>
                        <RadioGroup row aria-label="gender" 
                        name="row-radio-buttons-group"
                        value={customerExist}
                        onChange={(event)=>setCustomerExist(event.target.value)}>
                          <FormControlLabel value="yes" control={<Radio />} label="Yes" />
                          <FormControlLabel value="no" control={<Radio />} label="No" />
                        </RadioGroup>
                      </FormControl>
                      </Grid>
                  </Grid>
                  <br />
                  {(customerExist === 'no')?(<Grid 
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center" 
                    spacing="1">
                      <Grid item lg={6} md={6}>
                        <TextField
                          value={context.customerName}
                          onChange={(event)=>context.setCustomerName(event.target.value)}
                          fullWidth
                          variant="outlined"
                          label="Customer Name"
                        />
                      </Grid>
                      <Grid item lg={6} md={6}>
                        <TextField
                          value={context.customerPhone}
                          onChange={(event)=>context.setCustomerPhoneNumber(event.target.value)}
                          fullWidth
                          type="number"
                          variant="outlined"
                          label="Phone Number"
                        />
                      </Grid>
                      <Grid item lg={4} md={4}/>
                      <Grid item lg={4} md={4}/>
                  </Grid>):(<div></div>)}
                  <br />
                    {(customerExist === 'yes')?(<Grid 
                      container
                      direction="row"
                      justifyContent="center"
                      alignItems="center" 
                      spacing="1">
                        <Grid item lg={12} md={12}>
                          <Autocomplete
                              fullWidth
                              id="combo-box-demo"
                              options={customerList}
                              getOptionLabel={(option) => option ? `${option.name}` : ""}
                              renderInput={(data) => 
                              <TextField
                              {...data} 
                              label="Select Customer" 
                              variant="outlined" 
                              />}
                          />
                        </Grid>
                    </Grid>):(<div></div>)}
              </CardContent>
              <Divider/>
              <CardContent>
                  <Grid 
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center" 
                  spacing="1">
                    <Grid item lg={4} md={4}/>
                    <Grid item lg={3} md={3}/>
                    <Grid item lg={2} md={2}>
                        <Button
                          variant="contained"
                          color="success"
                          endIcon={<SaveOutlinedIcon/>}
                        >
                          Add
                        </Button>
                    </Grid>
                    <Grid item lg={1} md={1}/>
                    <Grid item lg={2} md={2}>
                        <Button
                            onClick={closeModal}
                            variant="contained"
                            color="error"
                            endIcon={<CloseOutlinedIcon/>}
                          >
                            Close
                          </Button>
                    </Grid>
                  </Grid>
              </CardContent>
          </Card>
          </Grid>
          <Grid item lg={3} md={3}/>
        </Grid>
      )}
  </PosContext.Consumer>
  );
};

export default React.memo(AddCustomerModal);
