import React from "react";
import { Grid, Card, Autocomplete } from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import { CardContent } from "@material-ui/core";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { TextField } from "@material-ui/core";
import customerList from "../POS/customerList";
const ViewCustomer = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
          <Grid container>
            <Grid item lg={6} md={6}>
                <h2>Customer Details</h2>
            </Grid>
            <Grid item lg={6} md={6}>
                <Autocomplete
                    fullWidth
                    id="combo-box-demo"
                    options={customerList}
                    getOptionLabel={(option) => option.name}
                    renderInput={(params) => <TextField  {...params} label="Search Customer" variant="outlined" />}
                    />
            </Grid>
          </Grid>
          <Grid container>
            <Grid 
            style={{ marginBottom: "15px" }}
            item 
            lg={12} md={12}>
                <Card>
                    <CardContent>
                    <Grid container>
                        <Grid item lg={12} md={12}>
                        <h2><AccountCircleIcon/> Harry</h2>
                        </Grid>
                    </Grid>
                    <Grid container>
                        <Grid item lg={6} md={6}>
                        <Grid container>
                            <Grid item lg={3} md={3}>
                            <h4><LocationOnIcon/>Address</h4>
                            <p>Harry,
                                Linking Street,
                                Phoenix, Arizona, USA </p>
                            </Grid>
                        </Grid>
                        <Grid container>
                            <Grid item lg={3} md={3}>
                            <h4><PhoneIphoneIcon/>Mobile</h4>
                            <p>(378) 400-1234 </p>
                            </Grid>
                        </Grid>
                        </Grid>
                        <Grid item lg={6} md={6}/>
                    </Grid>
                    </CardContent>
                </Card>
            </Grid>
          </Grid>
      </Grid>
  );
};

export default ViewCustomer;
