import React from "react";
import { Grid,Button } from "@material-ui/core";
import BackspaceIcon from '@material-ui/icons/Backspace';
import PosContext from "../../PosContext";
const PaymentAmountCalculatorOperations = () => {
    const buttonStyles = {
        padding:"18px 0",
        fontSize:"20px",
        background:"#1a237e"
    }
  return (
    <PosContext.Consumer>
        {context=>(
            <Grid 
            container
            direction="column"
            spacing="1">
                <Grid item lg={12} md={12}>
                    <Button
                    style={buttonStyles}
                    fullWidth
                    onClick={context.decreaseNumberFromPaidAmount}
                    variant="contained">
                        <BackspaceIcon/>
                    </Button>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Button
                    style={buttonStyles}
                    fullWidth
                    onClick={()=>context.addOperator("+")}
                    variant="contained">
                        +
                    </Button>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Button
                    style={buttonStyles}
                    fullWidth
                    onClick={()=>context.addOperator("-")}
                    variant="contained">
                        -
                    </Button>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Button
                    style={buttonStyles}
                    fullWidth
                    onClick={()=>context.addOperator("*")}
                    variant="contained">
                        *
                    </Button>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Button
                    style={buttonStyles}
                    fullWidth
                    onClick={()=>context.addOperator("/")}
                    variant="contained">
                        /
                    </Button>
                </Grid>
            </Grid>
        )}
    </PosContext.Consumer>
  );
}
export default PaymentAmountCalculatorOperations;
