import React from "react";
import { Grid} from "@material-ui/core";
import DashboardFilter from "../Components/DashboardFilter";
import DashboardCards from "../Components/DashboardCards";
import DashboardSalesAndPurchaseDue from "../Components/DashboardSalesAndPurchase";
import DashboardProductStockAlert from "../Components/ProductStockAlert";
import DashboardSalesOrder from "../Components/SalesOrderTable";
import PendingShipments from "../Components/PendingShipments";
import DashboardSales from "../Components/DashboardSales";
import DashboardSalesCurrentFinancialYear from "../Components/DashboardSalesCurrentFinancialYear";

const Dashboard = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Welcome Admin</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardCards/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardSales/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardSalesCurrentFinancialYear/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardSalesAndPurchaseDue/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardProductStockAlert/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <DashboardSalesOrder/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <PendingShipments/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default Dashboard;
