import React from "react";
import { Card, Grid, Select} from "@material-ui/core";
import { FormControl } from "@material-ui/core";
import { InputLabel } from "@material-ui/core";
import { MenuItem } from "@material-ui/core";
import AddSaleFilter from "../Components/Add Sale/AddSaleFilter";
import SaleDiscount from "../Components/SaleDiscount";
import SaleShippingDetails from "../Components/SaleShippingDetails";
import SaleAddPayment from "../Components/SaleAddPayment";
import SaleAddProduct from "../Components/SaleAddProduct";
import { Button } from "@material-ui/core";

const AddQuotation = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Add Quotation</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <Card>
                        <FormControl 
                            fullWidth 
                            variant="outlined">
                            <InputLabel id="demo-simple-select-outlined-label">Business Location</InputLabel>
                            <Select
                                labelId="demo-simple-select-outlined-label"
                                id="demo-simple-select-outlined"
                                // value={age}
                                // onChange={handleChange}
                                label="Business Location"
                            >
                            <MenuItem value="All">All</MenuItem>
                            <MenuItem value="Awesome Shop">Awesome Shop</MenuItem>
                            </Select>
                        </FormControl>
                    </Card>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <AddSaleFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SaleAddProduct/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SaleDiscount/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SaleShippingDetails/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SaleAddPayment/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <Button
                    fullWidth
                    variant="contained"
                    bgcolor="primary">
                        Save
                    </Button>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default AddQuotation;
