import React,{useState} from "react";
import { Grid,Button,Modal,MenuItem,Menu } from "@material-ui/core";
import { DataGrid } from '@material-ui/data-grid';
import EditIcon from '@material-ui/icons/Edit';
import AddBoxIcon from '@material-ui/icons/AddBox';
import { Divider } from "@material-ui/core";
import { useNavigate } from "react-router-dom";
import AddSupplierModal from "./Components/AddNewSupplierModal";
export const supplierList = [
  {id: 1,contactid: "C0001",businessname:"Manhattan Clothing Ltd",name:"Philip",email:"philip@gmail.com",
  taxnumber:"54869310093",payterm:"15 Days",openingbalance:"0.00",advancebalance:"0.00",addedon:"28/8/2021",
  address:"A-22/2", mobile:"+88015324568",totalpurchasedue:"0.00",totalpurchasereturndue:"0.00"},
  {id:2,contactid: "C0002",businessname:"Manhattan Clothing Ltd",name:"Alex",email:"alex@gmail.com",
  taxnumber:"54869310093",payterm:"15 Days",openingbalance:"0.00",advancebalance:"0.00",addedon:"28/8/2021",
  address:"A-22/2", mobile:"+88015324568",totalpurchasedue:"0.00",totalpurchasereturndue:"0.00"},
  {id: 3,contactid: "C0003",businessname:"Manhattan Clothing Ltd",name:"John Doe",email:"johndoe@gmail.com",
  taxnumber:"54869310093",payterm:"15 Days",openingbalance:"0.00",advancebalance:"0.00",addedon:"28/8/2021",
  address:"A-22/2", mobile:"+88015324568",totalpurchasedue:"0.00",totalpurchasereturndue:"0.00"},
  {id: 4,contactid: "C0004",businessname:"Manhattan Clothing Ltd",name:"Marina",email:"marina@gmail.com",
  taxnumber:"54869310093",payterm:"15 Days",openingbalance:"0.00",advancebalance:"0.00",addedon:"28/8/2021",
  address:"A-22/2", mobile:"+88015324568",totalpurchasedue:"0.00",totalpurchasereturndue:"0.00"},
  {id: 5,contactid: "C0005",businessname:"Manhattan Clothing Ltd",name:"Daniel",email:"daniel@gmail.com",
  taxnumber:"54869310093",payterm:"15 Days",openingbalance:"0.00",advancebalance:"0.00",addedon:"28/8/2021",
  address:"A-22/2", mobile:"+88015324568",totalpurchasedue:"0.00",totalpurchasereturndue:"0.00"}
]
const Suppliers = () => {
  const navigate = useNavigate();
    const columns = [
      { field: 'id', headerName: 'ID', width: 120 },
      {
        field: 'contactid',
        headerName: 'Contact ID',
        width: 180,
        editable: true,
      },
      {
        field: 'businessname',
        headerName: 'Business Name',
        width: 190,
        editable: true,
      },
      {
        field: 'name',
        headerName: 'Name',
        width: 150,
        editable: true,
      },
      {
        field: 'email',
        headerName: 'Email',
        width: 150,
        editable: true,
      },
      {
        field: 'taxnumber',
        headerName: 'taxnumber',
        width: 190,
        editable: true,
      },
      {
        field: 'payterm',
        headerName: 'Pay Term',
        width: 190,
        editable: true,
      },
      {
        field: 'openingbalance',
        headerName: 'Openingbalance',
        width: 190,
        editable: true,
      },
      {
        field: 'advancebalance',
        headerName: 'Advance Balance',
        width: 190,
        editable: true,
      },
      {
        field: 'addedon',
        headerName: 'Added On',
        width: 190,
        editable: true,
      },
      {
        field: 'address',
        headerName: 'Address',
        width: 190,
        editable: true,
      },
      {
        field: 'mobile',
        headerName: 'Mobile',
        width: 190,
        editable: true,
      },
      {
        field: 'totalpurchasedue',
        headerName: 'Total Purchase Due',
        width: 300,
        editable: true,
      },
      {
        field: 'totalpurchasereturndue',
        headerName: 'Total Purchase Return Due',
        width: 300,
        editable: true,
      }
    ];
  const onSelect = (event)=>{
    const {data} = event;
    const {id} = data;
    pushIdToSelectedList(id);
  }
  const [selectedIdList,setSelectedId] = useState([]);
  const pushIdToSelectedList = (id)=>{
      const includes = selectedIdList.includes(id);
      if(includes) {
        const updatedList = selectedIdList.filter((indexId)=> {return id!==indexId});
        setSelectedId(updatedList);
      }else{
          setSelectedId(prevIdList=>[...prevIdList,id]);
      }
  }
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const [addSupplierModalOpen,setAddSupplierModalOpen] = useState(false);
  const closeSupplierModal = ()=>{
    setAddSupplierModalOpen(false);
  }
  const openAddSupplierModalOpen = ()=>{
    setAddSupplierModalOpen(true);
  }
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid container >
                <Grid  item lg={3} md={3}>
                    <Button
                    aria-controls="simple-menu" 
                    aria-haspopup="true"
                    onClick={handleMenu}
                    
                    color="success" 
                    variant="contained"
                    startIcon={<EditIcon/>}
                    >
                        Actions
                    </Button>
                        <Menu
                            // style={{ width: "550px" }}
                            id="dd-menu"
                            anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                            anchorOrigin={{ horizontal: "left", vertical: "bottom" }}
                            transformOrigin={{ horizontal: "left", vertical: "top" }}
                            >
                            <MenuItem onClick={handleClose}>Pay</MenuItem>
                            <MenuItem onClick={()=>{navigate(`/supplier/${selectedIdList[0]}`)}}>View</MenuItem>
                            <MenuItem onClick={handleClose}>Edit</MenuItem>
                            <MenuItem onClick={handleClose}>Deleted</MenuItem>
                            <MenuItem onClick={handleClose}>Deactivate</MenuItem>
                            <Divider/>
                            <MenuItem onClick={handleClose}>Ledger</MenuItem>
                            <MenuItem onClick={handleClose}>Purchases</MenuItem>
                            <MenuItem onClick={handleClose}>Stock Report</MenuItem>
                        </Menu>
                </Grid>
                <Grid item lg={3} md={3}>
                <Button
                    aria-controls="simple-menu" 
                    aria-haspopup="true"
                    color="primary" 
                    variant="contained"
                    startIcon={<AddBoxIcon/>}
                    onClick={openAddSupplierModalOpen}
                    >
                        Add
                    </Button>
                    <Modal
                            open={addSupplierModalOpen}
                            onClose={closeSupplierModal}
                            aria-labelledby="simple-modal-title"
                            aria-describedby="simple-modal-description"
                            >
                            <AddSupplierModal 
                            closeModal={closeSupplierModal}/>
                        </Modal>
                </Grid>
            </Grid>
        </Grid>
        <Grid item lg={12} md={12}>
            <div style={{ height: 400, width: '100%' }}>
                <DataGrid
                    rows={supplierList}
                    columns={columns}
                    pageSize={5}
                    checkboxSelection
                    onRowSelected={onSelect}
                />
            </div>
        </Grid>
      </Grid>
  );
};

export default Suppliers;
