import { Card, InputLabel, Select } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { TextField } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
const SaleDiscount = ()=>{
    return (
        <>
            <Card>
                <CardContent>
                    <Grid 
                    container
                    spacing="2">
                        <Grid item lg={4} md={4}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Discount Type</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Discount Type"
                                >
                                <MenuItem value="">Please Select</MenuItem>
                                <MenuItem value="Percentage">Percentage</MenuItem>
                                <MenuItem value="Fixed">Fixed</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={4} md={4}>
                            <TextField 
                                fullWidth
                                type="number"
                                id="outlined-basic" 
                                label="Discount Amount" 
                                variant="outlined" />
                        </Grid>
                        <Grid item lg={1} md={1}/>
                        <Grid item lg={3} md={3}>
                            <p><strong>Discount Amount(-):0.00</strong></p>
                            <p><strong>Order Tax(+):0.00</strong></p>
                        </Grid>
                    </Grid>
                    <br />
                    <Grid 
                    container
                    direction="row"
                    spacing="2">
                        <Grid item lg={4} md={4}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Status</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Status"
                                >
                                <MenuItem value="Final">Final</MenuItem>
                                <MenuItem value="Draft">Draft</MenuItem>
                                <MenuItem value="Quotation">Quotation</MenuItem>
                                <MenuItem value="Proforma">Proforma</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={8} md={8}/>
                    </Grid>
                    <br />
                    <Grid 
                    container
                    direction="row"
                    spacing="2">
                        <Grid item lg={12} md={12}>
                            <TextField 
                                fullWidth
                                multiline
                                rows="6"
                                id="outlined-basic" 
                                label="Sell Note" 
                                variant="outlined" />
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </>
    )
}

export default SaleDiscount;