import { Card } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import { DataGrid } from '@material-ui/data-grid';
import React from 'react';
const stockAlertList = [
    {id: 1,location:"A",currentstock:"10.00"},
    {id: 2,location:"B",currentstock:"10.00"},
    {id: 3,location:"C",currentstock:"10.00"},
    {id: 4,location:"D",currentstock:"10.00"},
];
const DashboardProductStockAlert = ()=>{
    const stockAlertColumns = [
        { field: 'id', headerName: 'ID', width: 120 },
        {
          field: 'product',
          headerName: 'Product',
          width: 180,
          editable: true,
        },
        {
          field: 'location',
          headerName: 'Location',
          width: 150,
          editable: true,
        },
        {
          field: 'currentstock',
          headerName: 'Current Stock',
          width: 190,
          editable: true,
        },
      ];
    return (
        <>
            <Grid 
            direction="row"
            container
            spacing="2">
                <Grid item lg={12} md={12}>
                    <Card>
                        <div style={{ height: 400, width: '100%' }}>
                            <DataGrid
                                rows={stockAlertList}
                                columns={stockAlertColumns}
                                pageSize={5}
                                checkboxSelection
                            />
                        </div>
                    </Card>
                </Grid>
            </Grid>
        </>
    )
}

export default DashboardProductStockAlert;