import React,{useEffect} from "react";
import { Grid, Box, Container, Card} from "@material-ui/core";
import ItemCard from "./components/ItemCard";
import { useState } from "react";
import TableCard from "./components/TableCard";
import PosContext from "./PosContext";
import PosWorkList from "./components/PosWorkList";
import FoodTypeList from "./components/FoodTypeList";
import PosPaymentBox from "./components/PosPaymentBox";
import axios from "axios";
import getUrl from "./../../HTTP/url";
import { getAccessToken } from "./../../HTTP/token";
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
const POS = React.memo(() => {
  const [categoryList,setCategoryList] = useState([]);
    const getProductCategoryList = async()=>{
      const response = await axios.get(`${getUrl()}/category/list`,{headers: {Authorization: `Bearer ${getAccessToken()}`}});
      if(response.status===200){
        const {data} = response;
        setCategoryList(data);
      }
    }
    const [productList,setProductList] = useState([]);
    const getProductList = async()=>{
      const response = await axios.get(`${getUrl()}/product/list`,{headers: {Authorization: `Bearer ${getAccessToken()}`}});
      if(response.status===200){
        const {data} = response;
        setItems(data);
        setProductList(data);
      }
    }
    const getVat = async()=>{
      const response = await axios.get(`${getUrl()}/vat`,{headers: {Authorization: `Bearer ${getAccessToken()}`}});
      if(response.status===200){
        const {data} = response;
        setVat(data.value);
      }
    }
  useEffect(()=>{
    getProductCategoryList();
    getProductList();
    getVat();
  },[])
  const [invoiceItemList,setInvoiceItem] = useState([]);
  const onAddItem = (product)=>{
      const result = getInvoiceFilteredItems(product.id,invoiceItemList,true);
      if(result.length>0) {
        updateInvoiceProductData(product.id,true);
      }else{
        const item = getItemToAddInInvoice(product);
        setInvoiceItem([...invoiceItemList,item]);
      }
    }
  const updateInvoiceProductData = (id,increment = null,replace = false,productQuantity = 1)=>{
    const productIndex = invoiceItemList.findIndex((productItem=> productItem.id === id));
    let newProductList = [...invoiceItemList];
    let incrementGrandTotal;
    let incrementTotalItem;
    if(replace === true){
        let subTotalAmount,prevProductQuantity,updatedTotalItem,updatedGrandTotal;
        if(productQuantity === ""){
          prevProductQuantity = newProductList[productIndex].quantity;
          newProductList[productIndex].quantity = Number(productQuantity);
          subTotalAmount = 0;
          updatedTotalItem = (Number(totalItem)-Number(prevProductQuantity));
          updatedGrandTotal = (grandTotal+subTotalAmount)-newProductList[productIndex].subtotal;
          setTotalItem(updatedTotalItem);
          setGrandTotal(updatedGrandTotal);
        }else{
          prevProductQuantity = newProductList[productIndex].quantity;
          newProductList[productIndex].quantity = Number(productQuantity);
          subTotalAmount = Number(newProductList[productIndex].price) * Number(productQuantity);
          updatedTotalItem = (Number(totalItem)+Number(productQuantity))-Number(prevProductQuantity);
          updatedGrandTotal = (grandTotal+subTotalAmount)-newProductList[productIndex].subtotal;
          setGrandTotal(updatedGrandTotal);
          setTotalItem(updatedTotalItem);
        }
        newProductList[productIndex].subtotal = subTotalAmount;
    }else{
      if(increment){
        newProductList[productIndex].quantity += productQuantity;
        newProductList[productIndex].subtotal += newProductList[productIndex].price;
        incrementGrandTotal = true;
        incrementTotalItem = true;
        calculateGrossTotal(newProductList[productIndex].price,incrementGrandTotal);
        calculateTotalItem(incrementTotalItem);
      }else{
        if(newProductList[productIndex].quantity === 1){
          incrementGrandTotal = null;
          incrementTotalItem = null;
          calculateGrossTotal(newProductList[productIndex].price,incrementGrandTotal);
          calculateTotalItem(incrementTotalItem);
        }
        else{
          newProductList[productIndex].quantity -= productQuantity;
          newProductList[productIndex].subtotal -= newProductList[productIndex].price;
          incrementGrandTotal = false;
          incrementTotalItem = false;
          calculateGrossTotal(newProductList[productIndex].price,incrementGrandTotal);
          calculateTotalItem(incrementTotalItem);
        }
        
      }
    }
    
    setInvoiceItem(newProductList);
  }
  const getItemToAddInInvoice = (item)=>{
    const product = {
      id: item.id,
      name: item.name,
      price: item.price,
      quantity: 1,
      subtotal: item.price
    }
    calculateGrossTotal(item.price,true);
    calculateTotalItem(true);
    return product;
  }
  const [grandTotal,setGrandTotal] = useState(0);
  const [grossTotal,setGrossTotal] = useState(0);
  const [vat,setVat] = useState(0);
  const [vatAmount,setVatAmount] = useState();
  const calculateGrossTotal = (productAmount, add)=>{
    let totalAmount;
    if(add === null){
      totalAmount = grossTotal;
    }
    else if(add) totalAmount = grossTotal + productAmount;
    else totalAmount = grossTotal - productAmount;
    setGrossTotal(totalAmount);
  }
  const generateGrandTotal = async()=>{
    const vatPercentage = Number(vat) / 100;
    const vatAmount = grossTotal * vatPercentage;
    // setVatAmount(vatAmount);
    let grandTotal = grossTotal  + vatAmount;
    // setGrandTotal(Math.round(grandTotal));
    return {vatAmount,grandTotal};
  }
  const [totalItem,setTotalItem] = useState(0);
  const calculateTotalItem = (add, totalItemToReduce = 1)=>{
    let totalItemCount;
    if(add === null){
      totalItemCount = totalItem;
    }
    else if(add) totalItemCount = totalItem + totalItemToReduce;
    else totalItemCount = totalItem - totalItemToReduce;
    setTotalItem(totalItemCount);
  }
  const removeItem = (id)=>{
    const product = getInvoiceFilteredItems(id,invoiceItemList,true);
    const productSubTotal = product[0].subtotal;
    calculateGrossTotal(productSubTotal,false);
    calculateTotalItem(false,product[0].quantity);
    const updatedItems = getInvoiceFilteredItems(id,invoiceItemList,false);
    setInvoiceItem(updatedItems);
  }
  const getInvoiceFilteredItems = (id,data,equal)=>{
    let filteredData;
    if(equal) filteredData = data.filter((dataItem)=>{return dataItem.id ===id;});
    else filteredData = data.filter((dataItem)=>{return dataItem.id !==id;});
    return filteredData;
  }
  const productQuantityIncrement = (id)=>{
    updateInvoiceProductData(id,true);
  }
  const productQuantityDecrement = (id)=>{
    updateInvoiceProductData(id,false);
  }
  const date = Date();
  const [customerName,setCustomerName] = useState("");
  const [customerPhone,setCustomerPhoneNumber] = useState("");
  const [customerTableNo,setCustomerTableNo] = useState("");
  const [waiter,setWaiter] = useState("");
  const [guestNumber,setGuestNumber] = useState("");
  const [paidAmount,setPaidAmount] = useState(0);
  const addAmount = (number)=>{
    let amount = paidAmount === 0?"":paidAmount.toString();
    amount+=number;
    setPaidAmount(amount);
  }
  const addOperator = (operator)=>{
    let amountWithOperator = paidAmount + operator;
    setPaidAmount(amountWithOperator);
  }
  const decreaseNumberFromPaidAmount = ()=>{
    let amount = paidAmount === 0?"":paidAmount.toString();
    amount === 0 ? setPaidAmount(0) : amount.length === 1 ? setPaidAmount(0) :setPaidAmount(amount.substring(0, amount.length - 1));
  }
  const cleanPaidAmount = ()=>{
    setPaidAmount(0);
  }
  const calculateOperatorOperation = ()=>{
    let operator,amountArray;
    paidAmount.includes("+") ? operator = "+" : paidAmount.includes("-") ? operator = "-" : paidAmount.includes("*") ? operator = "*" : paidAmount.includes("/") ? operator = "/" : operator = null;
    if(operator!==null){
      amountArray = paidAmount.split(`${operator}`);
      const result = operator === "+"? Number(amountArray[0]) + Number(amountArray[1]) : operator === "-"? Number(amountArray[0]) - Number(amountArray[1]) : operator === "*"? Number(amountArray[0]) * Number(amountArray[1]):Number(amountArray[0]) / Number(amountArray[1]);
      setPaidAmount(result);
    }
  }
  const [items,setItems] = useState();
  const [paymentStatus,setPaymentStatus] = useState("Unpaid");
  const changePaymentStatus = (status)=>{
    setPaymentStatus(status);
    displaySuccessMessage(`Payment status changed to ${status}`);
  }
  const displaySuccessMessage = (message)=>{
        MySwal.fire({
            icon: 'success',
            title: 'Great',
            text: `${message}`
          })
    }
  const [paymentType,setPaymentType] = useState("Cash");
  const changePaymentType = (event)=>{
    setPaymentType(event.target.value);
    displaySuccessMessage(`Payment type changed to ${event.target.value}`);
  }
  const printInvoice = ()=>{
    window.print(<TableCard/>);
  }
  const [proccedToCheckout,setCheckout] = useState();
  const setProccedToCheckout = async(value)=>{
    if(value === false) {
      generateNewOrder();
      setCheckout(value);
    }
    else if(value === true){
      const result = await generateGrandTotal();
      const {vatAmount,grandTotal} = result;
      // await setVatAmount(vatAmount);
      // await setGrandTotal(grandTotal);
      await setCheckout(value);
      await checkOutOrder(vatAmount,grandTotal);
    }
  }
  const generateNewOrder = ()=>{
      setTotalItem(0);
      setGrandTotal(0);
      setGrossTotal(0);
      setVatAmount(0);
      setInvoiceItem([]);
  }
  const checkOutOrder = async(vatAmount,grandTotal)=>{
    // console.log(invoiceItemList);
    let orderedItems = [];
    setVatAmount(vatAmount);
    setGrandTotal(grandTotal);
    setPrintType("Customer");
    // eslint-disable-next-line array-callback-return
    invoiceItemList.map((item)=>{
      const product = {
        product: item.id,
        quantity: item.quantity,
        subtotal: item.subtotal
      }
      orderedItems.push(product);
    })
    const data = {
      complete: Boolean(false),
      grandtotal: Number(grandTotal),
      grosstotal: Number(grossTotal),
      vatamount: Number(vatAmount),
      token: "A35",
      items: orderedItems
    }
    const response = await axios.post(`${getUrl()}/order/checkout`,data,{headers: {Authorization: `Bearer ${getAccessToken()}`}});
    
    if(response.status === 201){
      MySwal.fire({
        icon: 'success',
        title: 'Great...',
        text: `${response.data}`,
      });
    }
  }
  const proccedToCheckoutDisplay = proccedToCheckout ? 'none': 'block';
  const filterItems = (category)=>{
    if(category==='All'||category==='all') setProductList(items);
    else{
      const filteredData = items.filter((item)=>{return item.category === category});
      setProductList(filteredData);
    }
  }
  const [printType,setPrintType] = useState("Customer")
  return (
    <Box style={{ margin:"0",paddingRight:"0",paddingLeft:"0",maxWidth:"100%" }}>
      <Container 
      style={{ margin:"0",paddingRight:"0",paddingLeft:"0",maxWidth:"100%" }}>
        <Grid container style={{ margin:"0",padding:"0",maxWidth:"100%" }}>
          <Grid item lg={12} md={12}>
            <Card>
              <div>
                <p>
                {/* <strong><LocationOnOutlinedIcon/> </strong> */}
                  <span>Somikoron, Main branch</span> 
                  <span> {date.toString()}</span> 
                </p>
              </div>
            </Card>
          </Grid>
        </Grid>
        {
          categoryList && productList &&
            <PosContext.Provider
            value={{ 
              onAddItem,removeItem,productQuantityIncrement,productQuantityDecrement,
              grandTotal,setGrandTotal,categoryList,printType,setPrintType,
              invoiceItemList,totalItem,addOperator,vatAmount,
              vat,grossTotal,calculateOperatorOperation,
              customerPhone,setCustomerPhoneNumber,customerName,setCustomerName,
              printInvoice,updateInvoiceProductData,customerTableNo,
              setCustomerTableNo,waiter,setWaiter,guestNumber,setGuestNumber,
              setProccedToCheckout,checkOutOrder,proccedToCheckout,paidAmount,addAmount,setPaidAmount,
              decreaseNumberFromPaidAmount,cleanPaidAmount,
              productList,paymentStatus,changePaymentStatus,paymentType,changePaymentType,
              }}>
              <Grid style={{ margin:"0",paddingRight:"0",paddingLeft:"0",maxWidth:"100%" }} 
              container 
              spacing={1}
              flexDirection={true}
              >
                <Grid item lg={1} md={12} sm={12} xs={12} style={{ display: proccedToCheckoutDisplay }}>
                  <PosWorkList/>
                </Grid>
                <Grid item lg={4} md={12} sm={12} xs={12}>
                    <TableCard/>
                </Grid>
                <Grid item lg={8} md={12} sm={12} xs={12} style={{ display: !proccedToCheckout ? 'none': 'block' }}>
                  <PosPaymentBox/>
                </Grid>
                <Grid item lg={1} md={12} sm={12} xs={12} style={{ display: proccedToCheckoutDisplay }}>
                  <FoodTypeList changeCategory={filterItems}/>
                </Grid>
                <Grid item lg={6} md={12} style={{ display: proccedToCheckoutDisplay }}>
                  <div style={{ maxHeight: "100vh",overflowY: "auto",
                  border:"1px solid rgba(0,0,0,0.3)",padding:"8px" }}>
                    <Container>
                      <ItemCard />
                    </Container>
                  </div>
                </Grid>
            </Grid>
            </PosContext.Provider>
        }
        
      </Container>
    </Box>
  );
});

export default POS;
