import React from "react";
import { Grid } from "@material-ui/core";
import SupplierAndCustomerReportFilter from "../Components/Supplier And Customer Report/SupplierAndCustomerReportFilter";
import SupplierAndCustomerReportTable from "../Components/Supplier And Customer Report/SupplierAndCustomerReportTable";

const CustomerAndSupplierReport = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Customer And Supplier Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SupplierAndCustomerReportFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <SupplierAndCustomerReportTable/>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default CustomerAndSupplierReport;
