import React, { useState } from "react";
import { Grid,Button, Modal } from "@material-ui/core";
import PosContext from "../PosContext";
import SelectPaymentTypeModal from "./SelectPaymentTypeModal";
const PaymentTypes = () => {
    const buttonStyles = {
        padding:"18px 0",
        fontSize:"20px",
        background:"#00695c"
    }
    const [paymentTypeModal,setPaymentTypeModalOpen] = useState(false);
    const openPaymentTypeModal = ()=>{
        setPaymentTypeModalOpen(true);
    }
    const closePaymentTypeModal = ()=>{
        setPaymentTypeModalOpen(false);
    }
  return (
    <PosContext.Consumer>
        {context=>(
            <Grid 
            container
            direction="column"
            spacing="1">
                <Grid item lg={12} md={12}>
                    <Button
                    style={{ padding: buttonStyles.padding,fontSize: buttonStyles.fontSize }}
                    color="success"
                    fullWidth
                    onClick={()=>context.changePaymentStatus("Paid")}
                    variant="contained">
                        Paid
                    </Button>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Button
                    style={{ padding: buttonStyles.padding,fontSize: buttonStyles.fontSize }}
                    color="warning"
                    fullWidth
                    onClick={()=>context.changePaymentStatus("Unpaid")}
                    variant="contained">
                        Unpaid
                    </Button>
                </Grid>
                <Grid item lg={12} md={12}>
                    <Button
                    style={buttonStyles}
                    fullWidth
                    onClick={openPaymentTypeModal}
                    variant="contained">
                        Select Payment
                    </Button>
                    <Modal
                        open={paymentTypeModal}
                        onClose={closePaymentTypeModal}
                        aria-labelledby="simple-modal-title"
                        aria-describedby="simple-modal-description"
                        >
                        <SelectPaymentTypeModal 
                        closeModal={closePaymentTypeModal}/>
                    </Modal>
                </Grid>
        </Grid>
        )}
    </PosContext.Consumer>
  );
}
export default PaymentTypes;
