import { Autocomplete, Card, InputLabel, Select } from '@material-ui/core';
import { MenuItem } from '@material-ui/core';
import { TextField } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import React from 'react';
import customerList from '../../../POS/customerList';
const userList = [
    {id:1,name: "All"},
    {id:2,name: "Admin"},
    {id:3,name: "Cashier"},
]
const DraftFilter = ()=>{
    return (
        <>
            <Card>
                <CardContent>
                    <Grid 
                    container
                    spacing="2">
                        <Grid item lg={3} md={3}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Business Location</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Business Location"
                                >
                                <MenuItem value="All">All</MenuItem>
                                <MenuItem value="Awesome Shop">Awesome Shop</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={3} md={3}>
                            <Autocomplete
                                fullWidth
                                id="combo-box-demo"
                                options={customerList}
                                getOptionLabel={(option) => option.name}
                                renderInput={(params) => <TextField  {...params} label="Customer" variant="outlined" />}
                            />
                        </Grid>
                        <Grid item lg={3} md={3}>
                            <FormControl 
                                fullWidth 
                                variant="outlined">
                                <InputLabel id="demo-simple-select-outlined-label">Payment Status</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Payment Status"
                                >
                                <MenuItem value="All">All</MenuItem>
                                <MenuItem value="Paid">Paid</MenuItem>
                                <MenuItem value="Due">Due</MenuItem>
                                <MenuItem value="Partial">Partial</MenuItem>
                                <MenuItem value="Overdue">Overdue</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={3} md={3}>
                            <Grid 
                            container
                            spacing="2">
                                <Grid item lg={6} md={6}>
                                    <TextField 
                                        fullWidth
                                        type="date"
                                        id="outlined-basic" 
                                        label="Start Date" 
                                        variant="outlined" />
                                </Grid>
                                <Grid item lg={6} md={6}>
                                    <TextField 
                                        fullWidth
                                        type="date"
                                        id="outlined-basic" 
                                        label="End Date" 
                                        variant="outlined" />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <br />
                    <Grid 
                    container
                    spacing="2">
                        <Grid item lg={3} md={3}>
                            <Autocomplete
                            fullWidth
                            id="combo-box-demo"
                            options={userList}
                            getOptionLabel={(option) => option.name}
                            renderInput={(params) => <TextField  {...params} label="Users" variant="outlined" />}
                            />
                        </Grid>
                        <Grid item lg={3} md={3}>
                                <FormControl 
                                    fullWidth 
                                    variant="outlined">
                                    <InputLabel id="demo-simple-select-outlined-label">Shipping Status</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        // value={age}
                                        // onChange={handleChange}
                                        label="Shipping Status"
                                    >
                                    <MenuItem value="All">All</MenuItem>
                                    <MenuItem value="Ordered">Ordered</MenuItem>
                                    <MenuItem value="Packed">Packed</MenuItem>
                                    <MenuItem value="Shipped">Shipped</MenuItem>
                                    <MenuItem value="Delivered">Delivered</MenuItem>
                                    <MenuItem value="Cancelled">Cancelled</MenuItem>
                                    </Select>
                                </FormControl>
                        </Grid>
                        <Grid item lg={6} md={6}/>
                    </Grid>
                </CardContent>
            </Card>
        </>
    )
}

export default DraftFilter;