import { Card } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import { DataGrid } from '@material-ui/data-grid';
import React from 'react';
const salesOrderList = [];
const DashboardSalesOrder = ()=>{
    const salesOrderColumns = [
        { field: 'id', headerName: 'ID', width: 120 },
        {
          field: 'date',
          headerName: 'Date',
          width: 180,
          editable: true,
        },
        {
          field: 'orderno',
          headerName: 'Order No',
          width: 150,
          editable: true,
        },
        {
          field: 'customer',
          headerName: 'Customer Name',
          width: 190,
          editable: true,
        },
        {
            field: 'contact',
            headerName: 'Contact Number',
            width: 190,
            editable: true,
          },
          {
            field: 'location',
            headerName: 'Location',
            width: 190,
            editable: true,
          },
          {
            field: 'status',
            headerName: 'Status',
            width: 190,
            editable: true,
          },
          {
            field: 'shippingstatus',
            headerName: 'Shipping Status',
            width: 190,
            editable: true,
          },
          {
            field: 'quantityremaining',
            headerName: 'Quantity Remaining',
            width: 190,
            editable: true,
          },
          {
            field: 'addedby',
            headerName: 'Added By',
            width: 190,
            editable: true,
          },
      ];
    return (
        <>
            <Grid 
            direction="row"
            container
            spacing="2">
                <Grid item lg={12} md={12}>
                    <Card>
                        <div style={{ height: 400, width: '100%' }}>
                            <DataGrid
                                rows={salesOrderList}
                                columns={salesOrderColumns}
                                pageSize={5}
                                checkboxSelection
                            />
                        </div>
                    </Card>
                </Grid>
            </Grid>
        </>
    )
}

export default DashboardSalesOrder;