import React from "react";
import { Card, Grid} from "@material-ui/core";
import ProfitLossFilter from "../Components/Profit Loss/ProfitLossFilter";
import ProfitLossReportTable from "../Components/Profit Loss/ProfitLossReportTable";
import ProfitSection from "../Components/Profit Loss/ProfitSection";
import ProfitLossTabs from "../Components/Profit Loss/ProfitLossTabs";

const ProfitLoss = () => {
  return (
      <Grid 
        container
        direction="row"
        justifyContent="center"
        alignItems="center" 
        spacing="1">
        <Grid 
        style={{ marginBottom: "15px" }}
        item 
        lg={12} md={12}>
            <Grid 
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center">
                <Grid item lg={6} md={6}>
                    <h2>Profit / Loss Report</h2>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <ProfitLossFilter/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <ProfitLossReportTable/>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12}>
                    <ProfitSection/>
                </Grid>
            </Grid>
            <br />
            <Grid container>
                <Grid item lg={12} md={12}>
                    <Card>
                        <ProfitLossTabs/>
                    </Card>
                </Grid>
            </Grid>
        </Grid>
      </Grid>
  );
};

export default ProfitLoss;
