import React from "react";
import { TableFooter,TableRow,TableCell,Table,TableHead,TableBody } from "@material-ui/core";
import PosContext from "../PosContext";
const InvoiceItemList = () => {
  return (
    <PosContext.Consumer>
        {context=>(
            <Table>
                <TableHead>
                <TableRow>
                    <TableCell>Product</TableCell>
                    <TableCell>Quantity</TableCell>
                    <TableCell>Price inc.tax</TableCell>
                    <TableCell>Subtotal</TableCell>
                </TableRow>
                </TableHead>
                <TableBody>
                    {
                    context.invoiceItemList && context.invoiceItemList.map((product)=>{
                        return (
                        <TableRow key={product.id}>
                            <TableCell>{product.name}</TableCell>
                            <TableCell>{product.quantity}</TableCell>
                            <TableCell>{product.price}</TableCell>
                            <TableCell>{product.subtotal}</TableCell>
                        </TableRow>   
                        )
                    })
                    }
                </TableBody>
                <TableFooter>
                <TableRow>
                    <TableCell><strong>Total Item: {context.totalItem}</strong></TableCell>
                    <TableCell><strong>Gross Total Amount: BDT {context.grossTotal}</strong></TableCell>
                    <TableCell/>
                    <TableCell/>
                </TableRow>
                <TableRow>
                    <TableCell><strong>Vat ({context.vat}%): BDT {context.vatAmount}</strong></TableCell>
                    <TableCell><strong>Grand Total: BDT {context.grandTotal}</strong></TableCell>
                    <TableCell/>
                </TableRow>
                </TableFooter>
            </Table>
        )}
    </PosContext.Consumer>
  );
};

export default React.memo(InvoiceItemList);
