import React from 'react';
import { Card, Grid, Typography } from '@material-ui/core';

const SalesRepresentativeSummary = ()=>{
    return (
        <>
            <Grid 
            container
            direction="row"
            spacing="2">
                <Grid item lg={12} md={12}>
                    <Card>
                    <Grid 
                    container
                    direction="row">
                        <h2>Summary</h2>
                        <Grid item lg={12} md={12}>
                            <Typography
                            variant="h2">
                                Total Sale - Total Sales Return: $12,300.00 - $0.00 = $ 12,300.00
                            </Typography>
                        </Grid>
                    </Grid>
                    <br />
                    <Grid 
                    container
                    direction="row">
                        <Grid item lg={12} md={12}>
                            <Typography
                            variant="h2">
                                Total Expense: $ 0.00  
                            </Typography>    
                        </Grid>
                    </Grid>
                    </Card>
                </Grid>
                <Grid item lg={12} md={12}> 
                    
                </Grid>
            </Grid>
        </>
    )
}

export default SalesRepresentativeSummary;