import React from "react";
import { Grid } from "@material-ui/core";
import PaymentAmountStatus from "./Sub-Components/PaymentAmountStatus";
import PaymentAmountCalculator from "./Sub-Components/PaymentAmountCalculator";
import PaymentTypes from "./PaymentTypes";
const PosPaymentBox = () => {
  return (
    <Grid 
    style={{ padding: "18px" }}
    container
    direction="row"
    spacing="2">
        <Grid item lg={10} md={12}>
            <Grid 
            style={{ padding: "18px" }}
            container
            direction="column"
            spacing="2">
                <Grid item lg={12} md={12}>
                    <PaymentAmountStatus/>
                </Grid>
                <Grid item lg={12} md={12}>
                    <PaymentAmountCalculator/>
                </Grid>
            </Grid>
        </Grid>
        <Grid item lg={2} md={12}>
            <PaymentTypes/>
        </Grid>
    </Grid>
  );
};

export default PosPaymentBox;
